<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form enctype="multipart/form-data">
      <div class="row">
        <div class="col-sm-5">
          <div class="form-group">
            <label>فایل صدا </label>
            <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
          </div>
          <span class="input_alert">{{showValidationTitle('file')}}</span>
          <div class="row">
            <div class="col-sm-6">
              <div class="form-group">
                <label>فایل عکس </label>
                <input type="file" id="pic" ref="myPic" class="custom-file-input" @change="previewImages">
              </div>
              <span class="input_alert">{{showValidationTitle('pic')}}</span>
            </div>
            <div class="col-sm-6">
              <div>
                <img class="thumb" v-if="url" :src="url"/>
              </div>
            </div>
          </div>
          <div class="form-group">
            <label>عنوان صدا</label>
            <input class="form-control" type="text" v-model="sound.title">
          </div>
          <span class="input_alert">{{showValidationTitle('title')}}</span>
          <div class="form-group">
            <label>انتخاب درس مربوطه</label>
            <v-select v-if="pageStatus === 'add' " v-model="sound.lessonId" :options="lessons"></v-select>
            <v-select v-else :value="selectedLesson" v-model="selectedLesson" :options="lessons"></v-select>
          </div>
          <span class="input_alert">{{showValidationTitle('lsnId')}}</span>
          <div class="form-group">
            <label>ترتیب صدا</label>
            <select class="form-control" v-model="sound.order">
              <option value="1">اول</option>
              <option value="2">دوم</option>
              <option value="3">سوم</option>
              <option value="4">چهارم</option>
              <option value="5">پنجم</option>
              <option value="6">ششم</option>
              <option value="7">هفتم</option>
              <option value="8">هشتم</option>
              <option value="9">نهم</option>
            </select>
          </div>
          <span class="input_alert">{{showValidationTitle('order')}}</span>
          <div class="form-group">
            <label>نوع صدا</label>
            <v-select v-if="pageStatus === 'add' " v-model="sound.type" :options="types"></v-select>
            <v-select v-else v-model="selectedType" :options="types" :value="selectedType"></v-select>
          </div>
          <span class="input_alert">{{showValidationTitle('type')}}</span>
        </div>
        <div class="col-sm-7">
          <div class="form-group">
            <label>متن صدا</label>
            <textarea class="form-control" rows="22" cols="30" v-model="sound.text"></textarea>
          </div>
        </div>
        <span class="input_alert">{{showValidationTitle('text')}}</span>
      </div>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeSound">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateSound(sound._id)">ویرایش</button>
    </form>
    <div class="row" style="margin-top: 20px;"
         v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import SoundApi from '../../services/api/sound/SoundApi'
  import LessonApi from '../../services/api/lesson/LessonApi'
  import TypeApi from '../../services/api/type/TypeApi'

  const lessonApi = new LessonApi()
  const soundApi = new SoundApi()
  const typeApi = new TypeApi()

  export default {
    name: 'AddSound',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' صدا',
        sound: {},
        url: null,
        images: {},
        pageStatus: 'add',
        lessons: [],
        types: [],
        selectedLesson: {},
        selectedType: {},
      }
    },
    methods: {
      updateSound (id) {
        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.sound.title)
        formData.append('typeId', this.selectedType.value)
        formData.append('lsnId', this.selectedLesson.value)
        formData.append('order', this.sound.order)
        formData.append('text', this.sound.text)
        formData.append('file', this.files)
        formData.append('pic', this.images)

        soundApi.updateSound(this,id,formData).then((response)=>{
          this.hiddenProgressBar()
          this.resetPercentage()
          this.sound = {}
          this.handleResponse(response.data.status_code, response.data.message)
        });
      },
      storeSound () {
        var formData = new FormData()
        this.showingProgressBar()
        formData.append('title', this.sound.title)
        formData.append('typeId', this.sound.type.value)
        formData.append('lsnId', this.sound.lessonId.value)
        formData.append('order', this.sound.order)
        formData.append('text', this.sound.text)
        formData.append('file', this.files)
        formData.append('pic', this.images)

        soundApi.create(this,formData).then((response)=>{
          this.hiddenProgressBar()
          this.resetPercentage()
          this.sound = {}
          this.handleResponse(response.data.status_code, response.data.message)
        });
      },
      getSound (id) {
        soundApi.getSound(id).then((response) => {
          this.sound = response.data.data
          this.selectedLesson = {
            label: response.data.data.lesson.title,
            value: response.data.data.lesson._id,
          }
          this.selectedType = {
            label: response.data.data.type.title,
            value: response.data.data.type._id,
          }
        })
      },
      previewImages () {
        this.images = this.$refs.myPic.files[0]
        this.url = URL.createObjectURL(this.$refs.myPic.files[0])
      },
      getSelectedLesson () {
        return this.selectedLesson
      },
      getLessonsSelective () {
        lessonApi.getLessonsSelective().then((response) => {
          this.lessons = response.data.data
        })
      },
      getTypesSelective () {
        typeApi.getTypesSelective().then((response) => {
          if (response.data.status_code == 200) {
            this.types = response.data.data
          }
        })
      },
    },
    created: function () {
      if (this.$route.params.soundid) {
        this.pageStatus = 'edit'
        this.getSound(this.$route.params.soundid)
      }
      this.getLessonsSelective()
      this.getTypesSelective()
    },
  }
</script>
