<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="chatsContainer">
        <div class="chatsInfoHeader">
          <div class="row">
            <div class="col-sm-4"><i class="fa fa-tag"></i> عنوان چت روم : {{ chatRoomName }}</div>
            <div class="col-sm-4">
              <a v-if="pinedMsg.text != ''" style="text-decoration: none" :href="'#'+pinedMsg._id">
                <p class="pinnedMsg">{{pinedMsg.text}}</p>
              </a>
            </div>
            <div class="col-sm-4" style="text-align: left"><i class="fa fa-group"></i> تعداد افراد این چت روم : {{
              userCount }}
            </div>
          </div>
        </div>
        <div class="bodyMsgContainer">
          <div class="bodyChatContainer" v-for="message in messages">
            <div class="row">
              <div class="col-sm-12">
                <div v-if="!message.voice" :id="message._id" class="msgContainer"
                     :class="getClassOfElement(message.user.role)">
                  <img @click="showMsgModal(message)" class="proChatImg" :src="message.user.avatarUrl">
                  <p class="msgText">{{message.msg}}</p>
                  <br>
                  <p v-if="message.user.role == 'student'" class="dateText">{{concatNameAndFamily(message.user.fname ,
                    message.user.lname)}} | {{convert_to_jalali(message.time)}}</p>
                  <p v-else class="dateText">{{message.user.name}} | {{convert_to_jalali(message.time)}}</p>
                </div>
                <div v-else :id="message._id" class="msgContainer"
                     :class="getClassOfElement(message.user.role)">
                  <img @click="showMsgModal(message)" class="proChatImg" :src="message.user.avatarUrl">
                  <p class="msgText">
                    <audio controls>
                      <source :src="message.voice" type="audio/mp3">
                      Your browser does not support the audio element.
                    </audio>
                  </p>
                  <br>
                  <p v-if="message.user.role == 'student'" class="dateText">{{concatNameAndFamily(message.user.fname ,
                    message.user.lname)}} | {{convert_to_jalali(message.time)}}</p>
                  <p v-else class="dateText">{{message.user.name}} | {{convert_to_jalali(message.time)}}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="textareaContainer">
        <textarea v-model="myMessage" class="form-control chatTextArea" rows="2"></textarea>
        <div class="row" style="height: 60px;">
          <div class="col-sm-2">
            <button @click="sendMsg" class="btn-success btn sendChatBtn">ارسال</button>
          </div>
          <div class="col-sm-2">
            <vue-dictaphone @stop="handleRecording($event)" mime-type="audio/mp3">
              <template slot-scope="{ isRecording, startRecording, stopRecording, deleteRecording }">
                <button class="btn btn-warning recordBtn" v-if="!isRecording"
                        @click="startRecording">ضبط صدا
                </button>
                <button class="btn btn-warning recordBtn" v-else @click="stopRecording">اتمام ضبط
                  و ارسال
                </button>
              </template>
            </vue-dictaphone>
          </div>
          <div class="col-sm-4">
            <template v-if="audioSource">
              <audio style="margin-top: 13px;" :src="audioSource" controls></audio>
            </template>
          </div>
          <div class="col-sm-4">
            <div class="dictaphone-spectrum-analyser">
              <vue-dictaphone-spectrum-analyser/>
            </div>
          </div>
        </div>
      </div>
    </div>
    <exampleModal :msg="clickedMsg" ref="modal" v-on:deleteMsg="handleDeleteMsg"
                  v-on:pinMsg="handlePinMsg" v-on:warnStudent="handleWarnStudent"
                  v-on:blockStudent="handleBlockStudent"></exampleModal>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import exampleModal from '../../components/modal'
  import Notify from '../../services/notifiction/Notification'
  import myMixin from '../../mixins/myMixin'
  import jalian from 'moment-jalaali'

  export default {
    name: 'ChatComponent',
    mixins: [myMixin],
    components: { Breadcrumb, exampleModal },
    data () {
      return {
        pageStatus: 'list',
        title: ' چت روم ',
        audioSource: null,
        chatRoomName: '',
        clickedMsg: {},
        userCount: '',
        loading: true,
        pinedMsg: {
          _id: '',
          text: '',
        },
        myMessage: '',
        messages: [],
        user: JSON.parse(localStorage.getItem('user')),
      }
    },
    sockets: {
      connect: function () {
        console.log('socket connected successfully')
        this.disableLoading()
      },
      disconnect: function () {
        console.log('socket disconnected')
        this.showLoading()
      },
      updateInfo: function (data) {
        // get chat info from serve
        this.disableLoading()
        var chatroomName = data.title
        var userCount = data.userCount
        var messages = data.allChat
        if(data.pin){
          var pinMsg = data.pin.msg
          this.pinedMsg.text = pinMsg
          var pinMsgId = data.pin._id
          this.pinedMsg._id = pinMsgId
        }

        this.chatRoomName = chatroomName
        this.userCount = userCount
        this.messages = messages



      },
      updateChat: function (data) {
        this.disableLoading()
        this.messages.push(data)
        this.scrollToBottom()
        this.playSendSound()
      },
      delMsg: function (data) {
        const status = data.status
        const deletedMsgId = data._id
        if (status === 'done') {
          const index = this.messages.findIndex(x => x._id === deletedMsgId)
          if (index > -1) {
            this.messages.splice(index, 1)
          }
          Notify.success('پیام با موفقیت حذف شد')
          this.playTrashSound()

        } else {
          Notify.error('پیام حذف نشد')
        }
      },
      pinMsg: function (data) {
        const status = data.status
        const msg = data.msg
        const id = data._id
        if (status === 'done') {
          this.pinedMsg.text = msg
          this.pinedMsg._id = id
          Notify.success('پیام با موفقیت پین شد')
        } else {
          Notify.error('پیام پین نشد')
        }
      },
      warnMsg: function (data) {

        const status = data.status
        const count = data.count
        if (status === 'done') {
          Notify.success('اخطار با موفقیت ثبت شد')
          if (count => 3) {
            //block user if count of warns are more than 3 warn
          }
        } else {
          Notify.error('خطار ثبت نشد')
        }
      },
      blockMsg: function (data) {
        const status = data.status
        const duration = data.blocked
        if (status === 'done') {
          if (duration == 0) {
            Notify.success('کاربر با موفقیت آزاد شد')
          }else{
            Notify.success('با موفقیت'+duration+'ساعت بلاک شد')
          }

        } else {
          Notify.error('عملیات بلاک با خطا روبرو شد')
        }

      }
    },
    methods: {
      getChatInfoFromSocket: function () {
        this.$socket.emit('getChatInfo', JSON.stringify({
          _id: this.user._id,
          chatroom: {
            _id: this.user.chatrooms[0].value,
            title: this.user.chatrooms[0].label,
          },
          chatAdmin: true
        }))
      },
      sendMsg () {
        this.$socket.emit('sendChat', JSON.stringify({ msg: this.myMessage.trim() }))
        this.myMessage = ''
        this.scrollToBottom()

      },
      deleteMsg (msgObject) {
        this.$socket.emit('delete', JSON.stringify({ msg: msgObject }))
      },
      showMsgModal (message) {
        this.clickedMsg = message
        let element = this.$refs.modal.$el
        $(element).modal('show')

      },
      handleDeleteMsg (msg) {
        let element = this.$refs.modal.$el
        $(element).modal('hide')
        const msgObject = msg
        this.deleteMsg(msgObject)

      },
      handlePinMsg (msg) {
        let element = this.$refs.modal.$el
        $(element).modal('hide')
        const msgObj = msg
        this.pinMessage(msgObj)
      },
      pinMessage (msgObj) {
        this.$socket.emit('pin', JSON.stringify({ msg: msgObj }))
      },
      handleWarnStudent (msg) {
        let element = this.$refs.modal.$el
        $(element).modal('hide')
        const msgObj = msg
        this.warnStudentMsg(msgObj)
      },
      warnStudentMsg (msgObj) {
        msgObj.caId = this.user._id
        this.$socket.emit('warn', JSON.stringify({ msg: msgObj }))
      },
      handleBlockStudent (msg) {
        let element = this.$refs.modal.$el
        $(element).modal('hide')
        const msgObj = msg
        this.blockStudent(msgObj)
      },
      blockStudent (msgObj) {
        msgObj.caId = this.user._id
        this.$socket.emit('block', JSON.stringify({ msg: msgObj }))
      },
      concatNameAndFamily (fname, lname) {
        return fname + ' ' + lname
      },
      scrollToBottom () {
        var container = this.$el.querySelector('.bodyMsgContainer')
        container.scrollTop = container.scrollHeight
      },
      getClassOfElement ($sender) {
        var cls = ''
        switch ($sender) {
          case 'admin':
            cls = 'rightFloat'
            break
          case 'student':
            cls = ''
            break
        }
        return cls
      },
      convert_to_jalali ($timestamp) {
        return jalian.utc($timestamp).format('jYYYY/jM/jD HH:MM:SS')
      },
      handleRecording ({ blob, src }) {
        this.audioSource = src
        this.convertToBase64(blob, (convertedBase64String) => {
          this.$socket.emit('sendVoice', convertedBase64String)
        })
      },
      convertToBase64 (blob, callback) {
        var reader = new FileReader()
        reader.onload = function () {
          var dataUrl = reader.result
          var base64 = dataUrl.split(',')[1]
          callback(base64)
        }
        reader.readAsDataURL(blob)
      },
      showSummary (str) {
        if (str) {
          var res = str.substr(1, 70)
          return res
        }
        return ''

      },
      playSendSound () {
        var audio = new Audio('./sound/piling.mp3')
        audio.play()
      },
      playTrashSound () {
        var audio = new Audio('./sound/trash.wav')
        audio.play()
      }

    },
    created () {
      this.getChatInfoFromSocket()
    },
    updated: function () {
      this.scrollToBottom()
    },
  }
</script>

