<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div class="row">
      <div class="col-sm-6">
        <form>
          <div class="form-group">
            <label>نام معلم</label>
            <input type="text" name="name" class="form-control" v-model="tutor.name"/>
            <span class="input_alert">{{showValidationTitle('name')}}</span>
          </div>
          <div class="form-group">
            <label>نام کاربری</label>
            <input type="text" name="username" class="form-control" v-model="tutor.username"/>
            <span class="input_alert">{{showValidationTitle('username')}}</span>
          </div>
          <div class="form-group">
            <label>سطح</label>
            <v-select multiple v-model="tutor.levels" :options="levels"></v-select>
          </div>

          <div class="form-group">
            <label>آپلود عکس </label>
            <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
          </div>

          <div ref="psw">
            <div class="form-group">
              <label>کلمه عبور</label>
              <input type="text" name="password" class="form-control" v-model="tutor.password"/>
              <span class="input_alert">{{showValidationTitle('password')}}</span>
            </div>
            <div class="form-group">
              <label>تکرار کلمه عبور</label>
              <input type="text" name="passwordRepeat" class="form-control" v-model="tutor.passwordRepeat"/>
              <span class="input_alert">{{showValidationTitle('passwordRepeat')}}</span>
            </div>
          </div>


          <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeTutor">افزودن</button>
          <button v-else class="btn btn-success" @click.prevent="updateTutor(tutor._id)">ویرایش</button>
        </form>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import TutorApi from '../../services/api/tutor/TutorApi'
  import myMixin from '../../mixins/myMixin'
  import LevelApi from '../../services/api/level/LevelApi'

  const levelApi = new LevelApi()
  const tutorApi = new TutorApi()

  export default {
    name: 'AddTutor',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' معلم',
        tutor: {},
        pageStatus: 'add',
        loading: false,
        levels: [],
      }
    },
    methods: {
      storeTutor () {
        if (this.validatePasswords(this.tutor.password, this.tutor.passwordRepeat)) {
          this.showLoading()
          var formData = new FormData()
          formData.append('name', this.tutor.name)
          formData.append('username', this.tutor.username)
          formData.append('password', this.tutor.password)
          formData.append('levels', JSON.stringify(this.tutor.levels))
          formData.append('file', this.files)
          tutorApi.create(this, formData).then((response) => {
            this.disableLoading()
            this.tutor = {}
            this.handleResponse(response.data.status_code, response.data.message)
          })
        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
          this.tutor = {}
        }
      },
      getTutor (id) {
        this.showLoading()
        tutorApi.getTutor(id).then((response) => {
          this.disableLoading()
          this.tutor = response.data.data
        })
      },
      updateTutor (id) {
        if (this.validatePasswords(this.tutor.password, this.tutor.passwordRepeat)) {
          tutorApi.updateTutor(id, this.tutor).then((response) => {
            this.handleResponse(response.data.status_code, response.data.message)
          })
        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
        }
      },
      getLevels () { //for select box
        levelApi.getLevelsSelective().then((response) => {
          this.levels = response.data.data
        })
      },
    },
    created: function () {
      if (this.$route.params.tutorid) {
        this.pageStatus = 'edit'
        this.getTutor(this.$route.params.tutorid)
      }
      this.getLevels()//for select box
    },
    mounted () {
      if (this.$route.params.tutorid) {
        this.$refs['psw'].classList.add('hidden')
      }
    },
  }
</script>
