<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div class="row">
      <div class="col-sm-6">
        <form>
          <div class="form-group">
            <label>نام مدیر</label>
            <input type="text" name="name" class="form-control" v-model="admin.name"/>
            <span class="input_alert">{{showValidationTitle('name')}}</span>
          </div>

          <div ref="psw">

            <div class="form-group">
              <label>نام کاربری</label>
              <input type="text" name="username" class="form-control" v-model="admin.username"/>
              <span class="input_alert">{{showValidationTitle('username')}}</span>
            </div>

            <div class="form-group">
              <label>کلمه عبور</label>
              <input type="text" name="password" class="form-control" v-model="admin.password"/>
              <span class="input_alert">{{showValidationTitle('password')}}</span>
            </div>
            <div class="form-group">
              <label>تکرار کلمه عبور</label>
              <input type="text" name="passwordRepeat" class="form-control" v-model="admin.passwordRepeat"/>
              <span class="input_alert">{{showValidationTitle('passwordRepeat')}}</span>
            </div>
          </div>

          <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeAdmin">افزودن</button>
          <button v-else class="btn btn-success" @click.prevent="updateAdmin(admin._id)">ویرایش</button>
        </form>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import AdminApi from '../../services/api/admin/AdminApi'
  import myMixin from '../../mixins/myMixin'

  const adminApi = new AdminApi()

  export default {
    name: 'AddAdmin',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' مدیر',
        admin: {},
        pageStatus: 'add',
        loading: false,
      }
    },
    methods: {
      storeAdmin () {
        if (this.validatePasswords(this.admin.password, this.admin.passwordRepeat)) {
          this.showLoading()
          adminApi.create(this.admin).then((response) => {
            this.disableLoading()
            this.admin = {}
            this.handleResponse(response.data.status_code, response.data.message)
          })
        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
          this.admin = {}
        }
      },
      getAdmin (id) {
        this.showLoading()
        adminApi.getAdmin(id).then((response) => {
          this.disableLoading()
          this.admin = response.data.data
        })
      },
      updateAdmin (id) {
        if (this.validatePasswords(this.admin.password, this.admin.passwordRepeat)) {
          adminApi.updateAdmin(id, {
            name:this.admin.name
          }).then((response) => {
            this.handleResponse(response.data.status_code, response.data.message)
          })
        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
        }
      },
    },
    created: function () {
      if (this.$route.params.adminid) {
        this.pageStatus = 'edit'
        this.getAdmin(this.$route.params.adminid)
      }
    },
    mounted () {
      if (this.$route.params.adminid) {
        this.$refs['psw'].classList.add('hidden')
      }
    },
  }
</script>
