<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>عنوان دپارتمان</label>
        <input type="text" name="title" class="form-control" v-model="department.title"/>
        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeDepartment">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateDepartment(department._id)">ویرایش</button>
    </form>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import DepartmentApi from '../../services/api/department/DepartmentApi'

  const departmentApi = new DepartmentApi()

  export default {
    name: 'AddDepartment',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' دپارتمان پشتیبانی',
        department: {},
        loading: false,
        pageStatus: 'add',
      }
    },
    methods: {
      storeDepartment () {
        this.showLoading()
        departmentApi.create(this.department).then((response) => {
          this.disableLoading()
          this.department = {}
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      getDepartment (id) {
        departmentApi.getDepartment(id).then((response) => {
          this.department = response.data.data
        })
      },
      updateDepartment (id) {
        departmentApi.updateDepartment(id, this.department).then((response) => {
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
    },
    created () {
      if (this.$route.params.depId) {
        this.pageStatus = 'edit'
        this.getDepartment(this.$route.params.depId)
      }
    }
  }
</script>

