<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام ساپورتر</th>
            <th>تصویر</th>
            <th>نام کاربری</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="supporter in supporters">
            <td>{{supporter.name}}</td>
            <td>
              <img class="thumb" v-bind:src="supporter.avatarUrl">
            </td>
            <td>{{supporter.username}}</td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditSupporter',params:{Supporterid:supporter._id}}"><i class="fa fa-edit"></i>
                </router-link>
                <a @click.prevent="deleteSupporter(supporter._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import SupporterApi from '../../services/api/supporter/SupporterApi'

  const supporterApi = new SupporterApi()
  export default {
    name: 'ListSupporters',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست ساپورتر ها',
        supporters: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getSupporters () {
        this.showLoading()
        supporterApi.getSupporters().then((response) => {
          this.supporters = response.data.data
          this.disableLoading()
        })
      },
      deleteSupporter (id) {
        this.showLoading()
        supporterApi.deleteSupporter(id).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getSupporters()
        })
      },
    },
    created () {
      this.getSupporters()
    }
  }
</script>
