<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered">
            <tr>
              <th>عنوان </th>
              <th>فایل ویدیو</th>
              <th>فایل زیر نویس</th>
              <th>ترتیب</th>
              <th>کاور</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="trick in tricks">
              <td>{{trick.title}}</td>
              <td><a :href="trick.url"><i class="fa fa-download"></i></a></td>
              <td><a :href="trick.srtUrl"><i class="fa fa-download"></i></a></td>
              <td>{{trick.order}}</td>
              <td>
                <img class="thumb" v-bind:src="trick.thumbUrl">
              </td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditTrick',params:{trickid:trick._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteTrick(trick._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import TrickApi from '../../services/api/trick/TrickApi'
  const trickApi = new TrickApi()

  export default {
    name: 'ListTricks',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست تیریک ها',
        tricks: {},
        pageStatus: 'list',
        loading: false,
        page: 1,
        limit: 5,
        totalPage: 1,
      }
    },
    methods: {
      clickCallback (pageNum) {
        this.page = pageNum
        this.tricks = {}
        this.getTricks()
      },
      getTricks () {
        this.showLoading()
        trickApi.getTricksByPaginate(this.page, this.limit).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.tricks = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      deleteTrick (id) {
        trickApi.deleteTrick(id).then((response) => {
          this.getTricks()
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
    },
    created () {
      this.getTricks()
    },
  }
</script>
