<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="item in blackList">
            <td>{{item.name}}</td>
            <!--<td>-->
              <!--<div class="op-icon">-->
                <!--<router-link :to="{name:'EditPackage',params:{packageid:packageItem._id}}"><i class="fa fa-edit"></i></router-link>-->
                <!--<a @click.prevent="deletePackage(packageItem._id)"><i class="fa fa-remove deleteIcon"></i></a>-->
              <!--</div>-->
            <!--</td>-->
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import ChatroomApi from '../../services/api/chatroom/ChatroomApi'
  import myMixin from '../../mixins/myMixin'

  const chatroomApi = new ChatroomApi()

  export default {
    name: 'BlockedUsers',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست سیاه ',
        blackList: {},
        loading: false,
        pageStatus: 'list',
        user: JSON.parse(localStorage.getItem('user')),
      }
    },
    methods: {
      getBlackLists () {
        this.showLoading()
        chatroomApi.getBlockedUsers(this.user.chatrooms[0].value).then((response) => {
          this.blackList = response.data.data
          this.disableLoading()
        })
      },
    },
    created () {
      this.getBlackLists()
    }
  }
</script>
