<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>عنوان دسته بندی</label>
        <input type="text" name="title" class="form-control" v-model="category.title">
        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>
      <button
        v-if="pageStatus === 'add' "
        class="btn btn-success"
        @click.prevent="storeCategory"
      >افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateCategory(category._id)">ویرایش</button>
    </form>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
import Breadcrumb from "../../components/Breadcrumb";
import myMixin from "../../mixins/myMixin";
import CategoryApi from "../../services/api/category/CategoryApi";
const categoryApi = new CategoryApi();

export default {
  name: "AddCategory",
  mixins: [myMixin],
  components: { Breadcrumb },
  data() {
    return {
      title: " دسته بندی",
      category: {},
      loading: false,
      pageStatus: "add"
    };
  },
  methods: {
    storeCategory() {
      this.showLoading();
      categoryApi.create(this.category).then(response => {
        this.disableLoading();
        this.category = {};
        this.handleResponse(response.data.status_code, response.data.message);
      });
    },
    getCategory(id) {
      categoryApi.getCategory(id).then(response => {
        this.category = response.data.data;
      });
    }
  },
  created() {
    if (this.$route.params.catid) {
      this.pageStatus = "edit";
      this.getCategory(this.$route.params.catid);
    }
  }
};
</script>
