<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row">
        <div class="col-sm-3">
          <div class="form-group">
            <label>فیلتر بر اساس درس</label>
            <v-select v-model="lessonIdFilter" :options="lessons" return-object></v-select>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered">
            <tr>
              <th>نام فایل</th>
              <th>نوع فایل</th>
              <th>درس مربطه</th>
              <th>فایل</th>
              <th>ترتیب</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="fileItem in files">
              <td>{{fileItem.title}}</td>
              <td>{{fileItem.type.title}}</td>
              <td>{{fileItem.lesson.title}}</td>
              <td><a :href="fileItem.url"><i class="fa fa-download"></i></a></td>
              <td>{{fileItem.order}}</td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditFile',params:{fileid:fileItem._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteFile(fileItem._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import FilesApi from '../../services/api/file/FileApi'
  import LessonApi from '../../services/api/lesson/LessonApi'

  const lessonApi = new LessonApi()
  const filesApi = new FilesApi()

  export default {
    name: 'ListFiles',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست فایل ها',
        files: {},
        lessons: [],
        pageStatus: 'list',
        loading: false,
        page: 1,
        limit: 5,
        totalPage: 1,
        lessonIdFilter: ''
      }
    },
    methods: {
      clickCallback (pageNum) {
        this.page = pageNum
        this.files = {}
        this.getFiles()
      },
      getFiles () {
        this.showLoading()
        filesApi.getFilesByPaginate(this.page, this.limit).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.files = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      deleteFile (id) {
        filesApi.deleteFile(id).then((response) => {
          this.getFiles()
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      getFilesByPaginateAndFilter (lsnid) {
        this.showLoading()
        filesApi.getFilesByPaginateAndFilter(this.page, this.limit, lsnid).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.files = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      getLessonsSelective () {
        lessonApi.getLessonsSelective().then((response) => {
          this.lessons = response.data.data
        })
      },
    },
    created () {
      this.getFiles()
      this.getLessonsSelective()
    },
    watch: {
      'lessonIdFilter': function () {
        const lsnId = this.lessonIdFilter
        if (!lsnId) {
          this.getFiles()
        } else {
          this.getFilesByPaginateAndFilter(this.lessonIdFilter.value)
        }
      }
    }
  }
</script>
