import axios from 'axios'

const API_URL = 'https://academi.rahasahamrah.com/api/lesson/'
const LESSON_SELECTIVE = "https://academi.rahasahamrah.com/api/lesson/selective"

export default class LevelApi {

  create (context , formData) {
    return axios.post(API_URL,
      formData,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
        onUploadProgress: function (progressEvent) {
          context.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
        }.bind(context)
      })
  }
  updateLesson (context , id, formData) {
    return axios.put(API_URL + id, formData, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
      onUploadProgress: function (progressEvent) {
        context.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
      }.bind(context)
    })
  }

  getLesson (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    });
  }


  deleteLesson(id) {
    return axios.delete(API_URL + id,
      {
        headers: {"Authorization":'Bearer ' + localStorage.getItem('jwt')}
      }
    );

  }

  getLessonsByPaginate (page,limit) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );

  }

  getLessonsSelective () {
    return axios.get(LESSON_SELECTIVE,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );

  }






}
