<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام چت ادمین</th>
            <th>تصویر</th>
            <th>نام کاربری</th>
            <th>چت روم مربوطه</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="chatAdmin in chatAdmins">
            <td>{{chatAdmin.name}}</td>
            <td>
              <img class="thumb" v-bind:src="chatAdmin.avatarUrl">
            </td>
            <td>{{chatAdmin.username}}</td>
            <td>{{formatChatRooms(chatAdmin.chatrooms)}}</td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditChatAdmin',params:{chatAdminId:chatAdmin._id}}"><i class="fa fa-edit"></i>
                </router-link>
                <a @click.prevent="deleteChatAdmin(chatAdmin._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import ChatAdminApi from '../../services/api/chatAdmin/ChatAdminApi'
  import myMixin from '../../mixins/myMixin'
  const chatAdminApi = new ChatAdminApi()

  export default {
    name: 'ListChatAdmins',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست ادمین چت',
        chatAdmins: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getChatAdmins () {
        this.showLoading()
        chatAdminApi.getChatAdmins().then((response) => {
          this.chatAdmins = response.data.data
          this.disableLoading()
        })
      },
      deleteChatAdmin (id) {
        this.showLoading()
        chatAdminApi.deleteChatAdmin(id).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getChatAdmins()
        })
      },
      formatChatRooms ($chatRoomsJsonArray) {
        var i
        var text = ''
        for (i = 0; i < $chatRoomsJsonArray.length; i++) {
          text += $chatRoomsJsonArray[i].label + ' - '
        }
        return text
      }
    },
    created () {
      this.getChatAdmins()
    }
  }
</script>
