<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]" v-for="question in questions">
      <div class="inner_question">
        <div class="row" style="direction: rtl">
          <div class="col-sm-6">
            <div class="inner_title_questions">
              <span v-if="question.type == 'quiz' "> <i class="fa fa-book"></i>  {{ question.lesson.label }}</span>
              <span v-else> <i class="fa fa-newspaper-o"></i> {{ question.exam.label }} </span>
            </div>
          </div>
          <div class="col-sm-6" style="direction: ltr">
            <div class="inner_title_questions">
              <div>
                <router-link :to="{name:'EditQuestion',params:{questionid:question._id}}"><i class="fa fa-edit"></i>
                </router-link>
                <a style="margin-left: 10px;" @click.prevent="deleteQuestion(question._id)"><i
                  class="fa fa-remove deleteIcon"></i></a>
              </div>
            </div>
          </div>
        </div>
        <div class="row" style="margin-top: 10px;">
          <div class="col-sm-12">
            <span style="color: blueviolet">{{ question.content }}</span>
          </div>
        </div>
        <div class="row" style="margin-top: 20px;">
          <div class="col-sm-3 optionsBlock">
            <span>{{question.answers[0].content}}</span> <i v-if="question.trueIndex == 0" class="fa fa-check trueAnswer"></i>
            <hr>
            <span class="descSpan">{{question.answers[0].description}}</span>
          </div>
          <div class="col-sm-3 optionsBlock">
            <span>{{question.answers[1].content}}</span> <i v-if="question.trueIndex == 1" class="fa fa-check trueAnswer"> </i>
            <hr>
            <span class="descSpan">{{question.answers[1].description}}</span>
          </div>
          <div class="col-sm-3 optionsBlock">
            <span>{{question.answers[2].content}}</span> <i v-if="question.trueIndex == 2" class="fa fa-check trueAnswer"></i>
            <hr>
            <span class="descSpan">{{question.answers[2].description}}</span>
          </div>
          <div class="col-sm-3 optionsBlock">
            <span>{{question.answers[3].content}}</span> <i v-if="question.trueIndex == 3" class="fa fa-check trueAnswer"></i>
            <hr>
            <span class="descSpan">{{question.answers[3].description}}</span>
          </div>
        </div>
      </div>
      <hr>
    </div>
    <div class="row">
      <div class="col-sm-6">
        <template>
          <paginate
            :page-count="totalPage"
            :page-range="3"
            :margin-pages="2"
            :click-handler="clickCallback"
            :prev-text="'Prev'"
            :next-text="'Next'"
            :container-class="'pagination'"
            :page-class="'page-item'">
          </paginate>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import QuestionApi from '../../services/api/question/QuestionApi'
  const questionApi = new QuestionApi()

  export default {
    name: 'ListQuestions',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست سوال ها',
        questions: {},
        pageStatus: 'list',
        loading: false,
        page: 1,
        limit: 5,
        totalPage: 1
      }
    },
    methods: {
      deleteQuestion (id) {
        questionApi.deleteQuestion(id).then((response) => {
          this.getQuestions()
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      clickCallback (pageNum) {
        this.page = pageNum
        this.questions = {}
        this.getQuestions()
      },
      getQuestions () {
        this.showLoading()
        questionApi.getQuestionsByPaginate(this.page, this.limit).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.questions = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
    },
    created () {
      this.getQuestions()
    }
  }
</script>

<style scoped>
  .inner_question {
    background-color: #faf2cc;
    padding: 10px;
    direction: ltr;
  }

  .inner_title_questions {
    background-color: #eae9c9;
    padding: 5px;
    width: fit-content;
  }

  .descSpan {
    color: #2e6da4;
    font-size: 10px;
  }

</style>
