<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>عنوان </label>
        <input type="text" name="title" class="form-control" v-model="package.name">
        <span class="input_alert">{{showValidationTitle('name')}}</span>
      </div>
      <div class="form-group">
        <label>قیمت </label>
        <input type="text" name="price" class="form-control" v-model="package.price">
        <span class="input_alert">{{showValidationTitle('price')}}</span>
      </div>

      <div class="form-group">
        <label>روز </label>
        <input type="text" name="day" class="form-control" v-model="package.days">
        <span class="input_alert">{{showValidationTitle('day')}}</span>
      </div>

      <div class="form-group">
        <label>نوع</label>
        <select v-model="package.type" class="form-control">
          <option value="vip">vip</option>
          <option v-if="isVipSelected" selected value="vip">vip</option>
          <option value="subscribe">subscribe</option>
          <option v-if="isSubscribeSelected" selected value="subscribe">subscribe</option>

        </select>
      </div>
      <span class="input_alert">{{showValidationTitle('type')}}</span>

      <div class="form-group">
        <label>توضیحات </label>
        <input type="text" name="description" class="form-control" v-model="package.description">
        <span class="input_alert">{{showValidationTitle('description')}}</span>
      </div>


      <div class="form-group">
        <label>درصد آفر </label>
        <input type="text" name="off" class="form-control" v-model="package.off.percent">
        <span class="input_alert">{{showValidationTitle('description')}}</span>
      </div>

      <div class="form-group">
        <label>توضیحات آفر</label>
        <input type="text" name="offdescription" class="form-control" v-model="package.off.description">
        <span class="input_alert">{{showValidationTitle('description')}}</span>
      </div>

      <button
        v-if="pageStatus === 'add' "
        class="btn btn-success"
        @click.prevent="storePackage"
      >افزودن
      </button>
      <button v-else class="btn btn-success" @click.prevent="updatePackage(package._id)">ویرایش</button>
    </form>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import PackageApi from '../../services/api/package/PackageApi'

  const packageApi = new PackageApi()

  export default {
    name: 'AddPackage',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'پکیج ',
        package: {
          off: {
            percent: ' '
          }
        },
        loading: false,
        pageStatus: 'add',
        types: [],
        selectedType: {}
      }
    },
    methods: {
      storePackage () {
        this.showLoading()
        packageApi.create(this.package).then(response => {
          this.disableLoading()
          this.package = {
            off: {
              percent: ' '
            }
          }
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      updatePackage (id) {
        this.showLoading()
        packageApi.updatePackage(id, this.package).then(response => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      getPackage (id) {
        packageApi.getPacakge(id).then(response => {

          if (response.data.data.off === null) {
            this.package = {
              _id: response.data.data._id,
              name: response.data.data.name,
              type: response.data.data.type,
              description: response.data.data.description,
              off: {
                percent: ' ',
                description: ' ',
              }
            }
          } else {
            this.package = response.data.data
          }

          this.selectedType = response.data.data.type
        })
      },
      isVipSelected () {
        if (this.selectedType === 'vip') {
          return true
        }
        return false
      },
      isSubscribeSelected () {
        if (this.selectedType === 'subscribe') {
          return true
        }
        return false
      }
    },
    created () {
      if (this.$route.params.packageid) {
        this.pageStatus = 'edit'
        this.getPackage(this.$route.params.packageid)
      }
    }
  }
</script>
