<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>عنوان</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="type in types">
            <td>{{type.label}}</td>
            <td>
              <div class="op-icon">
                <a @click.prevent="deleteType(type.value)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import TypesApi from '../../services/api/type/TypeApi'
  import myMixin from '../../mixins/myMixin'
  const typesApi = new TypesApi()

  export default {
    name: 'ListTypes',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست دسته ها',
        types: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getTypes () {
        this.showLoading();
        typesApi.getTypes().then((response) => {
          this.types = response.data.data
          this.disableLoading();
        })
      },
      deleteType (id) {
        this.showLoading();
        typesApi.deleteType(id).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getTypes();
        })
      },
    },
    created () {
      this.getTypes()
    }
  }
</script>
