import Notify from '../services/notifiction/Notification'
import ValidationErrors from '../ValidationErrors'

let myMixin = {
  data () {
    return {
      ValidationErrors: new ValidationErrors(),
      uploadPercentage: 0,
      files: {},
      showProgressBar: false,
      user: JSON.parse(localStorage.getItem('user'))
    }
  },
  methods: {
    isAdmin () {
      if (this.user.role === 'admin') {
        return true
      }
      return false
    },
    isCp () {
      if (this.user.role === 'cp') {
        return true
      }
      return false
    },
    isSupporter () {
      if (this.user.role === 'supporter') {
        return true
      }
      return false
    },
    isChatAdmin () {
      if (this.user.role === 'chatAdmin') {
        return true
      }
      return false
    },
    isTutor () {
      if (this.user.role === 'tutor') {
        return true
      }
      return false
    },

    disableLoading () {
      this.loading = false
    },
    showLoading () {
      this.loading = true
    },
    handleResponse (response_status_code, response_message) {
      switch (response_status_code) {
        case 201:
          Notify.success(response_message)
          break
        case 200:
          Notify.success(response_message)
          break
        case 422:
          this.ValidationErrors.store(response_status_code)
          Notify.error(response_message)
          break
      }
    },
    showValidationTitle (item) {
      return this.ValidationErrors.errorTitle(item)
    },
    validatePasswords (password, passwordRepeat) {
      if (password === passwordRepeat) {
        return true
      }
      return false
    },
    storeError (keyName, errorMsg) {
      const err = new Object()
      err[keyName] = [errorMsg]
      this.ValidationErrors.store(err)
    },
    previewFiles () {
      this.files = this.$refs.myFiles.files[0]
    },
    resetPercentage () {
      this.uploadPercentage = 0
    },
    hiddenProgressBar () {
      this.showProgressBar = false
    },
    showingProgressBar () {
      this.showProgressBar = true
    },
    doLogout () {
      localStorage.clear()
      this.$router.push({ path: '/login' })
    },
    getUserName () {
      return this.user.name
    },
  },
  computed: {
    uploadPercentageWithPercent () {
      return this.uploadPercentage.toString().concat('%')
    },
    getPercentage () {
      return this.uploadPercentage
    },

  },
}
export default myMixin

