import axios from 'axios'

const API_URL = 'api/ticket/'
const MY_TICKETS = 'https://academi.rahasahamrah.com/api/ticket/supporter/'



export default class TicketApi {

  create (ticket) {
    return axios.post(API_URL,
      ticket,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      })
  }

  getTicket (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })
  }

  updateTicket (id, ticket) {
    return axios.put(API_URL + id, ticket, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })

  }

  deleteTicket (id) {
    return axios.delete(API_URL + id,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )

  }

  getTicketsByPaginate (page, limit) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )

  }

  getAllTicketsBySupporterIdWithPaginate(page , limit , supporterId) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit + '&supId=' + supporterId,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )


  }

  getAllTicketsBySupporterIdWithPaginateAndFilter(page , limit , supporterId , $filter) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit + '&supId=' + supporterId + '&status=' + $filter ,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )


  }

  getAllMyTicketWithPaginate(page , limit , supporterId) {

    return axios.get(MY_TICKETS + supporterId + '/?page=' + page + '&limit=' + limit ,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )

  }
  getAllMyTicketWithPaginateAndStatusFilter(page , limit , supporterId , $filter) {

    return axios.get(MY_TICKETS + supporterId + '/?page=' + page + '&limit=' + limit + '&status=' + $filter ,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )

  }


}
