<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="chatsContainer">
        <div class="chatsInfoHeader">
          <div class="row">
            <div class="col-sm-3 col-sm-offset-9">
              <img class="proChatImg" :src="user.avatarUrl">
            </div>
          </div>
        </div>
        <div class="bodyMsgContainer">
          <div class="bodyChatContainer" v-for="(message , index) in messages" :key="index">
            <div class="row">
              <div class="col-sm-12">

                <div v-if="message.type == 'text' " :id="message._id" class="msgContainer"
                     :class="getClassOfElement(message.sender)">

                  <img v-if="message.sender == 'student' " class="proChatImg" :src="message.student.avatarUrl">
                  <img v-else class="proChatImg" :src="message.tutor.avatarUrl">


                  <p class="msgText">{{message.msg}}</p>
                  <br>
                  <p v-if="message.sender == 'student'" class="dateText">{{concatNameAndFamily(message.student.fname ,
                    message.student.lname)}} | {{convert_to_jalali(message.time)}}</p>
                  <p v-else class="dateText">{{message.student.name}} | {{convert_to_jalali(message.time)}}</p>
                </div>

                <div v-else-if="message.type == 'voice' " :id="message._id" class="msgContainer"
                     :class="getClassOfElement(message.sender)">


                  <img v-if="message.sender == 'student' " class="proChatImg" :src="message.student.avatarUrl">
                  <img v-else class="proChatImg" :src="message.tutor.avatarUrl">

                  <p class="msgText">
                    <audio controls>
                      <source :src="message.voice" type="audio/mp3">
                      Your browser does not support the audio element.
                    </audio>
                  </p>
                  <br>
                  <p v-if="message.sender == 'student'" class="dateText">{{concatNameAndFamily(message.student.fname ,
                    message.student.lname)}} | {{convert_to_jalali(message.time)}}</p>
                  <p v-else class="dateText">{{message.student.name}} | {{convert_to_jalali(message.time)}}</p>
                </div>


                <div v-else :id="message._id" class="msgContainer"
                     :class="getClassOfElement(message.sender)">

                  <img v-if="message.sender == 'student' " class="proChatImg" :src="message.student.avatarUrl">
                  <img v-else class="proChatImg" :src="message.tutor.avatarUrl">
                  <br>
                  <img @click="showBigImg(message.img)" id="myImg" class="msgImg" alt="عکس موجود نیست"
                       :src="message.img"/>
                  <p v-if="message.sender == 'student'" class="dateText">{{concatNameAndFamily(message.student.fname ,
                    message.student.lname)}} | {{convert_to_jalali(message.time)}}</p>
                  <p v-else class="dateText">{{message.student.name}} | {{convert_to_jalali(message.time)}}</p>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
      <div class="textareaContainer">
        <textarea v-model="myMessage" class="form-control chatTextArea" rows="2"></textarea>
        <div class="row" style="height: 60px;">
          <div class="col-sm-2">
            <button @click="sendMsg" class="btn-success btn sendChatBtn">ارسال</button>
          </div>
          <div class="col-sm-2">
            <vue-dictaphone @stop="handleRecording($event)" mime-type="audio/mp3">
              <template slot-scope="{ isRecording, startRecording, stopRecording, deleteRecording }">
                <button class="btn btn-warning recordBtn" v-if="!isRecording"
                        @click="startRecording">ضبط صدا
                </button>
                <button class="btn btn-warning recordBtn" v-else @click="stopRecording">اتمام ضبط
                  و ارسال
                </button>
              </template>
            </vue-dictaphone>
          </div>
          <div class="col-sm-2">
            <input type="file" @change="inputFileChange" ref="myImages" style="margin: 20px;">
          </div>
          <div class="col-sm-6">
            <div class="dictaphone-spectrum-analyser">
              <vue-dictaphone-spectrum-analyser/>
            </div>
          </div>
        </div>
      </div>
    </div>


    <div id="modal" class="modal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title"></h5>
            <button @click="closeTheModal" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img style="width: 100%;margin:0 auto;text-align: center" class="modal-content" id="img01">
          </div>
          <div class="modal-footer">
            <button @click="closeTheModal" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>

<script>


  import Breadcrumb from '../../components/Breadcrumb'
  import exampleModal from '../../components/modal'
  import myMixin from '../../mixins/myMixin'
  import jalian from 'moment-jalaali'

  export default {
    mixins: [myMixin],
    components: { Breadcrumb, exampleModal },
    name: 'TutorChat',
    props: ['messages', 'tutor', 'user'],
    data () {
      return {
        pageStatus: 'list',
        title: ' چت ',
        audioSource: null,
        loading: false,
        myMessage: '',
        imgFiles: {}
      }
    },
    sockets: {
      connect: function () {
        console.log('socket connected successfully')
        this.disableLoading()
      },
      disconnect: function () {
        console.log('socket disconnected')
        this.showLoading()
      },
      updatePVchat: function (data) { // listening for new chats from server
        this.messages.push(data)
        this.$forceUpdate()
      }
    },
    methods: {
      scrollToBottom () {
        var container = this.$el.querySelector('.bodyMsgContainer')
        container.scrollTop = container.scrollHeight
      },
      convertToBase64 (blob, callback) {
        var reader = new FileReader()
        reader.onload = function () {
          var dataUrl = reader.result
          var base64 = dataUrl.split(',')[1]
          callback(base64)
        }
        reader.readAsDataURL(blob)
      },
      convert_to_jalali ($timestamp) {
        return jalian.utc($timestamp).format('jYYYY/jM/jD HH:MM:SS')
      },
      handleRecording ({ blob, src }) {
        this.audioSource = src
        this.convertToBase64(blob, (convertedBase64String) => {
          this.$socket.emit('pvVoice', JSON.stringify(
            {
              base64: convertedBase64String,
              tutor: { _id: this.tutor._id },
              user: { _id: this.user._id }
            }
          ))
          console.log(convertedBase64String)
        })

      },
      getClassOfElement ($sender) {
        var cls = ''
        switch ($sender) {
          case 'tutor':
            cls = 'rightFloat'
            break
          case 'student':
            cls = ''
            break
        }
        return cls
      },
      concatNameAndFamily (fname, lname) {
        return fname + ' ' + lname
      },
      sendMsg () {
        this.$socket.emit('pvChat', JSON.stringify(
          {
            msg: this.myMessage.trim(),
            tutor: { _id: this.tutor._id },
            user: { _id: this.user._id }
          }))
        this.myMessage = ''
      },
      emmitFile (base64) {
        this.$socket.emit('pvImg', JSON.stringify(
          {
            base64: base64,
            tutor: { _id: this.tutor._id },
            user: { _id: this.user._id }
          }))
        this.myMessage = ''
      },
      showBigImg (chatImg) {
        // Get the modal
        var modal = document.getElementById('modal')
        var modalImg = document.getElementById('img01')
        modal.style.display = 'block'
        modalImg.src = chatImg
      },
      // Get the <span> element that closes the modal
      closeTheModal () {
        var modal = document.getElementById('modal')
        modal.style.display = 'none'
      },
      inputFileChange () {
        this.imgFiles = this.$refs.myImages.files[0]
        this.convertToBase64(this.imgFiles, (convertedBase64String) => {
          this.emmitFile(convertedBase64String)
          console.log(convertedBase64String)
        })
      },
    },
    created () {

    },
    updated: function () {
      this.scrollToBottom()
    },

  }
</script>

