    <br>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]" style="margin-top: 20px;">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row" v-if="isAdmin()">
        <div class="col-sm-3">
          <div class="inner_icons">
            <img class="hvr-grow" src="icon/presentation.svg"/>
            <h4>تعداد دانشجویان</h4>
            <h1>{{statistic.students}}</h1>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="inner_icons">
            <img class="hvr-grow" src="icon/video.svg"/>
            <h4>تعداد دروس</h4>
            <h1>{{statistic.lessons}}</h1>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="inner_icons">
            <img class="hvr-grow" src="icon/settings.svg"/>
            <h4>تعداد سطوح</h4>
            <h1>{{statistic.levels}}</h1>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="inner_icons">
            <img class="hvr-grow" src="icon/web-administrator.svg"/>
            <h4>تعداد مدیر ها</h4>
            <h1>{{statistic.admins}}</h1>
          </div>
        </div>
      </div>


      <div class="row" v-if="isCp()">
        <div class="col-sm-4">
          <div class="inner_icons">
            <img class="hvr-grow" src="icon/presentation.svg"/>
            <h4>تعداد دانشجویان</h4>
            <h1>{{statistic.students}}</h1>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="inner_icons">
            <img class="hvr-grow" src="icon/video.svg"/>
            <h4>تعداد دروس</h4>
            <h1>{{statistic.lessons}}</h1>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="inner_icons">
            <img class="hvr-grow" src="icon/settings.svg"/>
            <h4>تعداد سطوح</h4>
            <h1>{{statistic.levels}}</h1>
          </div>
        </div>
      </div>







      <div class="row" v-if="isSupporter()">
        <div class="col-sm-3">
          <div class="inner_icons">
            <font-awesome-icon icon="users"/>
            <h4>تیکت های من</h4>
            <h1>{{supporterStatistic.total}}</h1>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="inner_icons">
            <font-awesome-icon icon="book"/>
            <h4>تیکت های بسته شده</h4>
            <h1>{{supporterStatistic.closeCount}}</h1>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="inner_icons">
            <font-awesome-icon icon="bars"/>
            <h4>تیکت های باز</h4>
            <h1>{{supporterStatistic.openCount}}</h1>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="inner_icons">
            <font-awesome-icon icon="users"/>
            <h4>تیکت های جواب داده</h4>
            <h1>{{supporterStatistic.answeredCount}}</h1>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import DashboardApi from '../../services/api/dashboard/DashboardApi'
  import myMixin from '../../mixins/myMixin'

  const dashboardApi = new DashboardApi()
  export default {
    name: 'Dashboard',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'پنل مدیریت',
        statistic: {},
        supporterStatistic: {},
        loading: false,
        user: JSON.parse(localStorage.getItem('user'))
      }
    },
    methods: {

      getStatistic () {
        this.showLoading()
        dashboardApi.getStatistics().then((response) => {
          this.disableLoading()
          this.statistic = response.data.data
        })
      },
      getSupporterStatics () {
        this.showLoading()
        dashboardApi.getSupporterStatics(this.user._id).then((response) => {
          this.disableLoading()
          this.supporterStatistic = response.data.data
        })
      },
      isAdmin () {
        if (this.user.role == 'admin') {
          return true
        }
        return false
      },
      isSupporter () {
        if (this.user.role == 'supporter') {
          return true
        }
        return false
      }
    },
    created () {
      if (this.isSupporter()) {
        this.getSupporterStatics()
      } else {
        this.getStatistic()
      }
    }
  }
</script>

