<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row filterContainer">
      <div class="col-sm-12">
        <span style="margin-top: 5px">فیلتر کردن تیکت ها بر اساس وضعیت :</span>
        <table class="filterTable">
          <tr>
            <td>همه</td>
            <td><input checked type="radio" v-model="choosedFilterStatus" value="all"></td>
            <td>باز</td>
            <td><input type="radio" v-model="choosedFilterStatus" value="open"></td>
            <td>بسته</td>
            <td><input type="radio" v-model="choosedFilterStatus" value="closed"></td>
            <td>پاسخ داده شده</td>
            <td><input type="radio" v-model="choosedFilterStatus" value="answered"></td>
          </tr>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row">
        <div class="col-sm-6" v-for="ticket in tickets">
          <div class="ticker_container" :class="getBackground(ticket.status)">
            <table class="table ticketsTable">
              <tr>
                <td><i class="fa fa-tag"> موضوع : </i></td>
                <td>{{ticket.msg[0].title}}</td>
              </tr>
              <tr>
                <td><i class="fa fa-user"> ارسال کننده : </i></td>
                <td>{{getSenderFullName(ticket.student.fname , ticket.student.lname)}}</td>
              </tr>
              <tr>
                <td><i class="fa fa-calendar"> تاریخ ارسال : </i></td>
                <td>{{convert_to_jalali(ticket.time)}}</td>
              </tr>
              <tr>
              <tr>
                <td><i class="fa fa-bell"> وضعیت : </i></td>
                <td v-if="ticket.status == 'closed'">
                  <p style="width:80px;text-align: center"> بسته شده</p>
                </td>
                <td v-else-if="ticket.status == 'answered'">
                  <p style="width:130px;text-align: center">پاسخ داده شده</p>
                </td>
                <td v-else="ticket.status == 'open'">
                  <p style="width:80px;text-align: center"> باز</p>
                </td>
              </tr>
              <tr>
                <td>
                  <router-link class="btn btn-primary" :to="{name:'ListMessages',params:{ticketId:ticket._id}}"><i
                    class="fa fa-edit"> مشاهده جزپیات </i>
                  </router-link>
                </td>
                <td v-if="ticket.status != 'closed'">
                  <a class="btn btn-danger" @click.prevent="closeTicket(ticket._id)"><i class="fa fa-remove fa-ticket">
                    بستن تیکت </i></a>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-6">
        <template>
          <paginate
            :page-count="totalPage"
            :page-range="3"
            :margin-pages="2"
            :click-handler="clickCallback"
            :prev-text="'Prev'"
            :next-text="'Next'"
            :container-class="'pagination'"
            :page-class="'page-item'">
          </paginate>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import TicketApi from '../../services/api/ticket/TicketApi'
  const ticketApi = new TicketApi()
  import jalian from 'moment-jalaali'

  export default {
    name: 'ListMyTickets',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        user: JSON.parse(localStorage.getItem('user')),
        title: 'لیست تیکت های من',
        tickets: {},
        pageStatus: 'list',
        loading: false,
        choosedFilterStatus: '',
        page: 1,
        limit: 4,
        totalPage: 1,
      }
    },
    methods: {
      clickCallback (pageNum) {
        this.page = pageNum
        this.tickets = {}
        this.getTickets()
      },
      getBackground ($status) {
        var bgColor = '';
        switch ($status) {
          case 'answered':
            bgColor = 'greenBg'
            break
          case 'closed':
            bgColor = 'redBg'
            break
          case 'open':
            bgColor = 'yellowBg'
            break
        }
        return bgColor;
      },
      getTickets () {
        this.showLoading()
        ticketApi.getAllMyTicketWithPaginate(this.page, this.limit, this.user._id).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.tickets = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      getAllMyTicketWithPaginateAndStatusFilter ($status) {
        this.showLoading()
        ticketApi.getAllMyTicketWithPaginateAndStatusFilter(this.page, this.limit, this.user._id, $status).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.tickets = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      closeTicket ($ticketId) {
        this.showLoading()
        ticketApi.updateTicket($ticketId, { 'status': 'closed' }).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getTickets();
        })
      },
      convert_to_jalali ($timestamp) {
        return jalian.utc($timestamp).locale('fa').format('jYYYY/jM/jD HH:MM:SS')
      },
      getSenderFullName (fname, lname) {
        return fname + ' ' + lname
      }
    },
    watch: {
      'choosedFilterStatus': function () {
        switch (this.choosedFilterStatus) {
          case 'all' :
            this.getTickets()
            break
          default:
            this.getAllMyTicketWithPaginateAndStatusFilter(this.choosedFilterStatus)
        }
      }
    },
    created () {
      this.getTickets()
    },
  }
</script>

