<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>نام درس</label>
        <input type="text" name="title" class="form-control" v-model="lesson.title"/>
        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>
      <div class="form-group">
        <label>توضیحات درس</label>
        <input type="text" name="description" class="form-control" v-model="lesson.description"/>
        <span class="input_alert">{{showValidationTitle('description')}}</span>
      </div>
      <div class="form-group">
        <label>سطح درس</label>
        <select class="form-control" v-model="lesson.lvlId">
          <option v-for="level in levels" v-bind:value="level._id">{{ level.title }}</option>
        </select>
      </div>
      <div class="form-group">
        <label>مدت زمان آزمون درس</label>
        <input type="text" name="deadline" class="form-control" v-model="lesson.deadline"/>
        <span class="input_alert">{{showValidationTitle('deadline')}}</span>
      </div>
      <div class="form-group">
        <label>ترتیب </label>
        <select class="form-control" v-model="lesson.order">
          <option value="1">اول</option>
          <option value="2">دوم</option>
          <option value="3">سوم</option>
          <option value="4">چهارم</option>
        </select>
        <span class="input_alert">{{showValidationTitle('order')}}</span>
      </div>
      <div class="form-group">
        <label>آپلود عکس </label>
        <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
      </div>
      <span class="input_alert">{{showValidationTitle('file')}}</span>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeLesson">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateLesson(lesson._id)">ویرایش</button>
    </form>
    <div class="row" style="margin-top: 20px;" v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import LessonApi from '../../services/api/lesson/LessonApi'
  import LevelApi from '../../services/api/level/LevelApi'

  const lessonApi = new LessonApi()
  const levelApi = new LevelApi()
  export default {
    name: 'AddLesson',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' درس',
        lesson: {},
        levels: {},
        pageStatus: 'add',
      }
    },
    methods: {
      storeLesson () {
        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.lesson.title)
        formData.append('description', this.lesson.description)
        formData.append('lvlId', this.lesson.lvlId)
        formData.append('order', this.lesson.order)
        formData.append('deadline', this.lesson.deadline)
        formData.append('file', this.files)

        lessonApi.create(this,formData).then((response)=>{
          this.hiddenProgressBar()
          this.resetPercentage()
          this.handleResponse(response.data.status_code, response.data.message)
          this.lesson = {}
        });


      },
      getLesson (id) {
        lessonApi.getLesson(id).then((response) => {
          this.lesson = response.data.data
        })
      },
      updateLesson (id) {
        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.lesson.title)
        formData.append('description', this.lesson.description)
        formData.append('lvlId', this.lesson.lvlId)
        formData.append('order', this.lesson.order)
        formData.append('deadline', this.lesson.deadline)
        formData.append('file', this.files)

        lessonApi.updateLesson(this,id,formData).then((response)=>{
          this.hiddenProgressBar()
          this.resetPercentage()
          this.handleResponse(response.data.status_code, response.data.message)
          this.lesson = {}
        });
      },
      getLevels () { //for select box
        levelApi.getLevels().then((response) => {
          this.levels = response.data.data
        })
      },
    },
    created () {
      if (this.$route.params.lessonid) {
        this.pageStatus = 'edit'
        this.getLesson(this.$route.params.lessonid)
      }
      this.getLevels()//for select box
    }
  }
</script>
