<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام معلم</th>
            <th>نام کاربری</th>
            <th>تصویر</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="tutor in tutors">
            <td>{{tutor.name}}</td>
            <td>{{tutor.username}}</td>
            <td>
              <img class="thumb" v-bind:src="tutor.avatarUrl">
            </td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditTutor',params:{tutorid:tutor._id}}"><i class="fa fa-edit"></i></router-link>
                <a @click.prevent="deleteTutor(tutor._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import TutorApi from '../../services/api/tutor/TutorApi'
  import myMixin from '../../mixins/myMixin'
  const tutorApi = new TutorApi()
  export default {
    name: 'ListTutor',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست معلم ها',
        tutors: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getTutors () {
        this.showLoading();
        tutorApi.getTutors().then((response) => {
          this.tutors = response.data.data
          this.disableLoading();
        })
      },
      deleteTutor (id) {
        this.showLoading();
        tutorApi.deleteTutor(id).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getTutors()
        })
      },
    },
    created () {
      this.getTutors()
    }
  }
</script>
