<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div  v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row">
        <div class="col-sm-3">
          <div class="form-group">
            <label>فیلتر بر اساس درس</label>
            <v-select v-model="lessonIdFilter" :options="lessons" return-object></v-select>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered">
            <tr>
              <th>نام صدا</th>
              <th>نوع صدا</th>
              <th>درس مربطه</th>
              <th>فایل</th>
              <th>ترتیب</th>
              <th>عکس</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="sound in sounds">
              <td>{{sound.title}}</td>
              <td>{{sound.type.title}}</td>
              <td>{{sound.lesson.title}}</td>
              <td>
                <audio controls>
                  <source :src="sound.url" type="audio/mpeg">
                  Your browser does not support the audio element.
                </audio>
              </td>
              <td>{{sound.order}}</td>
              <td>
                <img class="thumb" v-bind:src="sound.coverUrl" alt=" بدون کاور ">
              </td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditSound',params:{soundid:sound._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteSound(sound._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import SoundApi from '../../services/api/sound/SoundApi'
  import LessonApi from '../../services/api/lesson/LessonApi'
  const lessonApi = new LessonApi()
  const soundApi = new SoundApi()

  export default {
    name: 'ListSounds',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست صدا ها',
        sounds: {},
        lessons:[],
        pageStatus: 'list',
        loading:false,
        page:1,
        limit:5,
        totalPage:1,
        lessonIdFilter: ''
      }
    },
    methods: {
      clickCallback (pageNum) {
        this.page = pageNum
        this.sounds = {}
        this.getSounds()
      },
      getSounds () {
        this.showLoading();
        soundApi.getSoundsByPaginate(this.page,this.limit).then((response) => {
          this.disableLoading();
          if (response.data.status_code == 200) {
            this.sounds = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      deleteSound(id){
        soundApi.deleteSound(id).then((response) => {
          this.getSounds()
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      getSoundsByPaginateAndFilter (lsnid) {
        this.showLoading()
        soundApi.getSoundsByPaginateAndFilter(this.page, this.limit,lsnid).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.sounds = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      getLessonsSelective () {
        lessonApi.getLessonsSelective().then((response) => {
          this.lessons = response.data.data
        })
      },
    },
    created () {
      this.getSounds()
      this.getLessonsSelective()
    },
    watch: {
      'lessonIdFilter': function () {
        const lsnId =  this.lessonIdFilter;
        if(!lsnId)
        {
          this.getSounds()
        }else{
          this.getSoundsByPaginateAndFilter(this.lessonIdFilter.value)
        }
      }
    }
  }
</script>
