<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div class="row">
      <div class="col-sm-4" v-for="requestedUser in requestedUsers">
        <div class="inner_certificate_block">
          <div style="text-align: center"><img style="width: 80px;border-radius: 50%" :src="requestedUser.avatarUrl">
          </div>
          <table class="table certTable">
            <tr>
              <td>نام :</td>
              <td>{{requestedUser.fname}}</td>
            </tr>
            <tr>
              <td>نام خانوادگی :</td>
              <td>{{requestedUser.lname}}</td>
            </tr>
            <tr>
              <td>امتیاز :</td>
              <td>{{requestedUser.score}}</td>
            </tr>
            <tr>
              <td>شناسه :</td>
              <td>{{requestedUser._id}}</td>
            </tr>
            <tr>
              <td>تاریخ:</td>
              <td>
                {{convert_to_jalali(requestedUser.time)}}
              </td>
            </tr>
          </table>
          <table class="table">
            <tr>
              <td>
                <button @click="acceptRequest(requestedUser)" class="btn btn-success">تایید</button>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import myMixin from '../../mixins/myMixin'
  import Breadcrumb from '../../components/Breadcrumb'
  import jalalian from 'moment-jalaali'

  export default {
    mixins: [myMixin],
    name: 'ToturRequestsComponent',
    components: { Breadcrumb },
    data () {
      return {
        pageStatus: 'list',
        title: 'درخواست های معلم خصوصی',
        loading: false,
        requestedUsers: [],
        user: JSON.parse(localStorage.getItem('user')),
      }
    },
    sockets: {
      connect: function () {
        console.log('socket connected successfully')
        this.disableLoading()
      },
      connected: function (data) {
        console.log(data)
        this.requestedUsers.push(data.conversations)

      },
      disconnect: function () {
        console.log('socket disconnected')
        this.showLoading()
      },
      requested: function (data) {
        //data of user who wants the teacher and teacher should accept it . you must add it to
        console.log(data.conversations)
        this.requestedUsers.push(data)

      },
      accepted: function (data) { // triggers when I accepted a chat
        console.log(data) // data that contains allChats = [ ]  and tutor and user objects
        this.$router.push({
          name: 'TutorChat',
          params: { messages: data.allMessages, tutor: data.tutor, user: data.user }
        })

      }
    },
    methods: {
      connectToturToSocket: function () {
        this.$socket.emit('tutor', JSON.stringify({ data: this.user }))
      },
      convert_to_jalali ($timestamp) {
        return jalalian.utc($timestamp).format('jYYYY/jM/jD')
      },
      acceptRequest (requestedUser) {
        this.$socket.emit('acceptChat', JSON.stringify({
          data: {
            user: requestedUser,
            tutor: this.user
          }
        }))
      },
    },
    created () {
      this.connectToturToSocket()
    }
  }
</script>

