<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div class="row">
      <div class="col-sm-6">
        <form>
          <div class="form-group">
            <label>نام ساپورتر</label>
            <input type="text" name="name" class="form-control" v-model="supporter.name"/>
            <span class="input_alert">{{showValidationTitle('name')}}</span>
          </div>
          <div class="form-group">
            <label>نام کاربری</label>
            <input type="text" name="username" class="form-control" v-model="supporter.username"/>
            <span class="input_alert">{{showValidationTitle('username')}}</span>
          </div>

          <div ref="psw">
            <div class="form-group">
              <label>کلمه عبور</label>
              <input type="text" name="password" class="form-control" v-model="supporter.password"/>
              <span class="input_alert">{{showValidationTitle('password')}}</span>
            </div>
            <div class="form-group">
              <label>تکرار کلمه عبور</label>
              <input type="text" name="passwordRepeat" class="form-control" v-model="supporter.passwordRepeat"/>
              <span class="input_alert">{{showValidationTitle('passwordRepeat')}}</span>
            </div>
          </div>

          <div class="form-group">
            <label>دپارتمان مربوطه</label>
            <v-select multiple v-model="supporter.department" :options="departments"></v-select>
          </div>
          <span class="input_alert">{{showValidationTitle('department')}}</span>
          <div class="form-group">
            <label>آپلود عکس </label>
            <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
          </div>
          <span class="input_alert">{{showValidationTitle('file')}}</span>
          <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeSupporter">افزودن</button>
          <button v-else class="btn btn-success" @click.prevent="updateSupporter(supporter._id)">ویرایش</button>
        </form>
      </div>
    </div>
    <div class="row" style="margin-top: 20px;" v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import SupporterApi from '../../services/api/supporter/SupporterApi'
  import DepartmentApi from '../../services/api/department/DepartmentApi'
  const supporterApi = new SupporterApi()
  const departmentApi = new DepartmentApi()
  export default {
    name: 'AddSupporter',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' ساپورتر',
        supporter: {
          department:[]
        },
        pageStatus: 'add',
        departments: [],
      }
    },
    methods: {
      storeSupporter () {
        if (this.validatePasswords(this.supporter.password, this.supporter.passwordRepeat)) {
          this.showingProgressBar()
          var formData = new FormData()
          formData.append('name', this.supporter.name)
          formData.append('username', this.supporter.username)
          formData.append('password', this.supporter.password)
          formData.append('file', this.files)
          formData.append('department',JSON.stringify(this.supporter.department))

          supporterApi.create(this,formData).then((response)=>{
            this.hiddenProgressBar()
            this.resetPercentage()
            this.supporter = {}
            this.handleResponse(response.data.status_code, response.data.message)
          });

        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
          this.supporter = {}
        }
      },
      getSupporter (id) {
        this.showLoading()
        supporterApi.getSupporter(id).then((response) => {
          this.disableLoading()
          this.supporter = response.data.data
        })
      },
      updateSupporter (id) {
        if (this.validatePasswords(this.supporter.password, this.supporter.passwordRepeat)) {
          this.showingProgressBar()
          var formData = new FormData()
          formData.append('name', this.supporter.name)
          formData.append('file', this.files)
          formData.append('department',JSON.stringify(this.supporter.department))
          supporterApi.updateSupporter(this,id,formData).then((response)=>{
            this.hiddenProgressBar()
            this.resetPercentage()
            this.supporter = {}
            this.handleResponse(response.data.status_code, response.data.message)
          });

        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
        }
      },
      getDepartmentsSelective () {
        departmentApi.getDepartmentsSelective().then((response) => {
          if (response.data.status_code == 200) {
            this.departments = response.data.data
          }
        })
      }
    },
    created () {
      if (this.$route.params.Supporterid) {
        this.pageStatus = 'edit'
        this.getSupporter(this.$route.params.Supporterid)
      }
      this.getDepartmentsSelective()
    },
    mounted () {
      if (this.$route.params.Supporterid) {
        this.$refs['psw'].classList.add('hidden')
      }
    },
  }
</script>

