<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>عنوان</th>
            <th>قیمت</th>
            <th>درصد آفر</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="packageItem in packages">
            <td>{{packageItem.name}}</td>
            <td>{{packageItem.price}}</td>
            <td v-if="isPacakgeOfferIsNull(packageItem.off) === false">
              {{packageItem.off.percent + ' % '}}
            </td>
            <td v-else>0 %</td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditPackage',params:{packageid:packageItem._id}}"><i class="fa fa-edit"></i>
                </router-link>
                <a @click.prevent="deletePackage(packageItem._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import PackageApi from '../../services/api/package/PackageApi'
  import myMixin from '../../mixins/myMixin'

  const packageApi = new PackageApi()

  export default {
    name: 'ListPackages',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست پکیج ها',
        packages: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getPackages () {
        this.showLoading()
        packageApi.getPackages().then((response) => {
          this.packages = response.data.data
          this.disableLoading()
        })
      },
      deletePackage (id) {
        this.showLoading()
        packageApi.deletePackage(id).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getPackages()
        })
      },
      isPacakgeOfferIsNull (val) {
        if (val === null) {
          return true
        }
        return false
      }
    },
    created () {
      this.getPackages()
    }
  }
</script>
