<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered">
            <tr>
              <th>نام آزمون</th>
              <th>زمان آزمون</th>
              <th>کاور</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="exam in exams">
              <td>{{exam.title}}</td>
              <td>{{exam.time}}</td>
              <td>
                <img class="thumb" v-bind:src="exam.avatarUrl" alt=" بدون کاور ">
              </td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditExam',params:{examid:exam._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteExam(exam._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import ExamApi from '../../services/api/exam/ExamApi'
  const examApi = new ExamApi()
  export default {
    name: 'ListExams',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست آزمون ها',
        exams: {},
        pageStatus: 'list',
        loading: false,
        page: 1,
        limit: 5,
        totalPage: 1,
      }
    },
    methods: {
      clickCallback (pageNum) {
        this.page = pageNum
        this.exams = {}
        this.getExams()
      },
      getExams () {
        this.showLoading()
        examApi.getExamsPaginate(this.page, this.limit).then((response) => {
          this.disableLoading()
          this.exams = response.data.data
          this.totalPage = response.data.totalPage
        })
      },
      deleteExam (id) {
        examApi.deleteExam(id).then((response) => {
          this.handleResponse(response.data.status_code, response.data.message)
          this.getExams()
        })
      },
    },
    created () {
      this.getExams()
    }
  }
</script>

