import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

var Paginate = require('vuejs-paginate')

import axios from 'axios'
import vSelect from 'vue-select'
import ProgressBar from 'vue-simple-progress'
import VueSocketIO from 'vue-socket.io'
import Spinner from 'vue-spinkit'
import VueDictaphone from "vue-dictaphone";
Vue.use(VueDictaphone);
Vue.component('Spinner', Spinner)

Vue.prototype.$http = axios
Vue.config.productionTip = false
Vue.component('paginate', Paginate)
Vue.component('v-select', vSelect)
Vue.component('ProgressBar', ProgressBar)

import { library } from '@fortawesome/fontawesome-svg-core'
import { faCoffee, faUsers, faBook, faGraduationCap, faBars, faUserSecret } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faCoffee, faUsers, faBook, faGraduationCap, faBars, faUserSecret)

Vue.component('font-awesome-icon', FontAwesomeIcon)


//return to login if token expired
axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  if (JSON.stringify(error.response.status) == 401) {
    localStorage.clear()
    router.push({ path: '/login' })
  }
})


Vue.use(new VueSocketIO({
  debug: true,
  connection: 'https://academi.rahasahamrah.com',
  reconnection:true
}))


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
