<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form enctype="multipart/form-data">
      <div class="row">
        <div class="col-sm-4">
          <div class="form-group">
            <label>عنوان متن</label>
            <input class="form-control" type="text" v-model="text.title">
          </div>
          <span class="input_alert">{{showValidationTitle('title')}}</span>
          <div class="form-group">
            <label>انتخاب درس مربوطه</label>
            <v-select v-if="pageStatus === 'add' " v-model="text.lsnId" :options="lessons"></v-select>
            <v-select v-else :value="selectedLesson" v-model="selectedLesson" :options="lessons"></v-select>
          </div>
          <span class="input_alert">{{showValidationTitle('lsnId')}}</span>
          <div class="form-group">
            <label>نوع نوشته</label>
            <v-select v-if="pageStatus === 'add' " v-model="text.typeId" :options="types"></v-select>
            <v-select v-else v-model="selectedType" :options="types" :value="selectedType"></v-select>
          </div>
          <span class="input_alert">{{showValidationTitle('typeId')}}</span>
        </div>
        <div class="col-sm-8">
          <div class="form-group">
            <label>متن</label>
            <textarea v-if="pageStatus === 'add' " class="form-control" rows="16" cols="30"
                      v-model="text.description"></textarea>
            <textarea v-else class="form-control" rows="16" cols="30" v-model="text.description"></textarea>
          </div>
        </div>
        <span class="input_alert">{{showValidationTitle('description')}}</span>
      </div>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeText">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateText(text._id)">ویرایش</button>
    </form>
    <div class="row" style="margin-top: 20px;"
         v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import TextApi from '../../services/api/text/TextApi'
  import LessonApi from '../../services/api/lesson/LessonApi'
  import TypeApi from '../../services/api/type/TypeApi'

  const lessonApi = new LessonApi()
  const textApi = new TextApi()
  const typeApi = new TypeApi()

  export default {
    name: 'AddText',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' متن',
        text: {},
        pageStatus: 'add',
        lessons: [],
        types: [],
        selectedLesson: {},
        selectedType: {},
      }
    },
    methods: {
      updateText (id) {
        textApi.updateText(id,
          {
            description: this.text.description,
            lsnId: this.selectedLesson.value,
            typeId: this.selectedType.value,
            title: this.text.title,
          }
        ).then((response) => {
          this.disableLoading()
          this.text = {}
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      storeText () {
        textApi.create(
          {
            description: this.text.description,
            lsnId: this.text.lsnId.value,
            typeId: this.text.typeId.value,
            title: this.text.title,
          }
        ).then((response) => {
          this.disableLoading()
          this.text = {}
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      getText (id) {
        textApi.getText(id).then((response) => {
          this.text = response.data.data
          this.selectedLesson = {
            label: response.data.data.lesson.title,
            value: response.data.data.lesson._id,
          }
          this.selectedType = {
            label: response.data.data.type.title,
            value: response.data.data.type._id,
          }
        })
      },
      getSelectedLesson () {
        return this.selectedLesson
      },
      getLessonsSelective () {
        lessonApi.getLessonsSelective().then((response) => {
          this.lessons = response.data.data
        })
      },
      getTypesSelective () {
        typeApi.getTypesSelective().then((response) => {
          this.types = response.data.data
        })
      },
    },
    created: function () {
      if (this.$route.params.textid) {
        this.pageStatus = 'edit'
        this.getText(this.$route.params.textid)
      }
      this.getLessonsSelective()
      this.getTypesSelective()
    },
  }
</script>
