<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div class="row">
      <div class="col-sm-6">
        <form>

          <div class="form-group">
            <label>کلمه عبور فعلی</label>
            <input type="text" name="password" class="form-control" v-model="credential.password"/>
            <span class="input_alert">{{showValidationTitle('password')}}</span>
          </div>

          <div class="form-group">
            <label>کلمه عبور جدید</label>
            <input type="text" name="newPassword" class="form-control" v-model="credential.newPassword"/>
            <span class="input_alert">{{showValidationTitle('newPassword')}}</span>
          </div>
          <div class="form-group">
            <label>تکرار کلمه عبور جدید</label>
            <input type="text" name="passwordRepeat" class="form-control" v-model="credential.passwordRepeat"/>
            <span class="input_alert">{{showValidationTitle('passwordRepeat')}}</span>
          </div>

          <button class="btn btn-success" @click.prevent="changePassword">تغییر</button>
        </form>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import PasswordApi from '../../services/api/password/PasswordApi'

  const passwordApi = new PasswordApi()
  export default {
    name: 'ChangePassword',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' تغییر پسورد',
        credential: {
          password: ' '
        },
        user: JSON.parse(localStorage.getItem('user')),
        pageStatus: 'add',
        loading: false,
      }
    },
    methods: {
      changePassword () {
        if (this.credential.password !== ' ') {
          if (this.validatePasswords(this.credential.newPassword, this.credential.passwordRepeat)) {
            if (this.credential.newPassword.length > 5) {
              passwordApi.update(this.user._id , {
                oldPassword : this.credential.password,
                newPassword : this.credential.newPassword,
                role:this.user.role
              }).then((response) => {
                this.handleResponse(response.data.status_code, response.data.message)
              })
            } else {
              this.storeError('newPassword', 'کلمه عبور باید بیشتر از 5 حرف باشد')
            }
          } else {
            this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
          }
        } else {
          this.storeError('password', 'فیلد خالی است لطفا پسورد فعلی را وارد کنید')
        }
      }
    },

  }
</script>
