<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div class="row">
      <div class="col-sm-6">
        <form>
          <div class="form-group">
            <label>عنوان</label>
            <input type="text" name="title" class="form-control" v-model="type.title">
            <span class="input_alert">{{showValidationTitle('title')}}</span>
          </div>
          <div class="form-group" ref="lessonSelectDiv">
            <label>انتخاب نوع محتوای مربوطه</label>
            <v-select multiple v-model="type.category" :options="categories"></v-select>
          </div>
          <button
            v-if="pageStatus === 'add' "
            class="btn btn-success"
            @click.prevent="storeType"
          >افزودن</button>
        </form>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
import Breadcrumb from "../../components/Breadcrumb";
import TypeApi from "../../services/api/type/TypeApi";
import myMixin from "../../mixins/myMixin";
import CategoryApi from "../../services/api/category/CategoryApi";
const categoryApi = new CategoryApi();
const typeApi = new TypeApi();

export default {
  name: "AddType",
  mixins: [myMixin],
  components: { Breadcrumb },
  data() {
    return {
      title: "دسته اصلی",
      type: {},
      categories: [],
      pageStatus: "add",
      loading: false
    };
  },
  methods: {
    storeType() {
      this.showLoading();
      typeApi.create(this.type).then(response => {
        this.disableLoading();
        this.type = {};
        this.handleResponse(response.data.status_code, response.data.message);
      });
    },
    getCategories() {
      categoryApi.getCategories().then(response => {
        this.categories = response.data.data;
      });
    }
  },
  created() {
    this.getCategories();
  }
};
</script>
