import axios from 'axios'
const API_URL = 'api/certificate/'
export default class CertificateApi{

  getCertificate (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })
  }

  updateCertificate (id, certificate) {
    return axios.put(API_URL + id, certificate, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })

  }

  getCertificates () {
    return axios.get(API_URL,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }

  deleteCertificate (id) {
    return axios.delete(API_URL + id,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }

  getCertificatesByPaginate (page,limit) {
    return axios.get(API_URL + '?page=' + page + '&limit=' + limit,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );
  }

}
