<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>عنوان</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="department in departments">
            <td>{{department.label}}</td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditDepartment',params:{depId:department.value}}"><i class="fa fa-edit"></i>
                </router-link>
                <a @click.prevent="deleteDepartment(department.value)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import DepartmentApi from '../../services/api/department/DepartmentApi'
  const departmentApi = new DepartmentApi()
  export default {
    name: 'ListDepartments',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست دپارتمان',
        departments: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getDepartments () {
        this.showLoading()
        departmentApi.getDepartments().then((response) => {
          this.departments = response.data.data
          this.disableLoading()
        })
      },
      deleteDepartment (id) {
        this.showLoading()
        departmentApi.deleteDepartment(id).then((response) => {
          this.disableLoading()
          this.getDepartments()
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
    },
    created () {
      this.getDepartments()
    }
  }
</script>
