<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form>
      <div class="row">
        <div class="col-sm-4">
          <div class="form-group">
            <label>نوع سوال</label>
            <v-select v-if="pageStatus === 'add' " v-model="question.typeId" :options="types" return-object></v-select>
            <v-select v-else  :value="selectedType" v-model="question.type" :options="types" ></v-select>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="form-group" ref="lessonSelectDiv">
            <label>انتخاب درس مربوطه</label>
            <v-select v-if="pageStatus === 'add' " v-model="question.lesson" :options="lessons"></v-select>
            <v-select v-else :value="selectedLesson" v-model="question.lesson" :options="lessons"></v-select>
          </div>
        </div>
        <div class="col-sm-4">
          <div class="form-group" ref="quizSelectDiv">
            <label>انتخاب آزمون مربوطه</label>
            <v-select id="examsSelect" v-if="pageStatus === 'add' " v-model="question.exam" :options="exams"></v-select>
            <v-select v-else :value="selectedExam" v-model="question.exam" :options="exams"></v-select>
          </div>
        </div>
      </div>
      <div class="row" style="margin-top: 20px;">
        <div class="col-sm-12">
          <div class="inner_options_block">
            <div class="form-group">
              <label>امتیاز سوال</label>
              <input required placeholder="مقدار را به صورت عدد وارد کنید" class="form-control" type="text" v-model="question.score">
            </div>
          </div>
        </div>
      </div>
      <div class="row" style="margin-top: 20px;">
        <div class="col-sm-6">
          <div class="inner_options_block">
            <div class="form-group">
              <label>آپلود فایل صوتی صورت سوال </label>
              <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
            </div>
          </div>
        </div>
        <div class="col-sm-6">
          <div class="inner_options_block">
            <audio controls id="sound">
            </audio>
          </div>
        </div>
      </div>
      <div class="row" style="margin-top: 20px;">
        <div class="col-sm-12">
          <div class="inner_options_block">
            <div class="form-group">
              <label>صورت سوال</label>
              <input required class="form-control" type="text" v-model="question.content">
            </div>
          </div>
        </div>
      </div>
      <div class="row" style="margin-top: 20px">
        <div class="col-sm-6">
          <div class="inner_options_block">
            <div class="form-group">
              <label>گزینه 1</label>
              <input required class="form-control" type="text" v-model="question.answers[0].content">
            </div>
            <div class="form-group">
              <span>توضیحات گزینه یک</span>
              <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text" v-model="question.answers[0].description">
            </div>
            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="0" v-model="question.trueIndex">
            </div>
          </div>
        </div>
        <div class="col-sm-6">
          <div class="inner_options_block">
            <div class="form-group">
              <label>گزینه 2</label>
              <input required class="form-control" type="text" v-model="question.answers[1].content">
            </div>
            <div class="form-group">
              <div class="form-group">
                <span>توضیحات گزینه دو</span>
                <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text" v-model="question.answers[1].description">
              </div>
            </div>
            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="1" v-model="question.trueIndex">
            </div>
          </div>
        </div>
      </div>
      <div class="row" style="margin-top: 20px">
        <div class="col-sm-6">
          <div class="inner_options_block">
            <div class="form-group">
              <label>گزینه 3</label>
              <input required class="form-control" type="text" v-model="question.answers[2].content">
            </div>
            <div class="form-group">
              <span>توضیحات گزینه سه</span>
              <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text" v-model="question.answers[2].description">
            </div>
            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="2" v-model="question.trueIndex">
            </div>
          </div>
        </div>
        <div class="col-sm-6">
          <div class="inner_options_block">
            <div class="form-group">
              <label>گزینه 4</label>
              <input required class="form-control" type="text" v-model="question.answers[3].content">
            </div>
            <div class="form-group">
              <div class="form-group">
                <span>توضیحات گزینه چهار</span>
                <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text" v-model="question.answers[3].description">
              </div>
            </div>
            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="3" v-model="question.trueIndex">
            </div>
          </div>
        </div>
      </div>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeQuestion">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateQuestion(question._id)">ویرایش</button>
    </form>
    <div class="row" style="margin-top: 20px;" v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import LessonApi from '../../services/api/lesson/LessonApi'
  import QuestionApi from '../../services/api/question/QuestionApi'
  import ExamApi from '../../services/api/exam/ExamApi'
  import TypeApi from '../../services/api/type/TypeApi'

  const lessonApi = new LessonApi()
  const questionApi = new QuestionApi()
  const examApi = new ExamApi()
  const typesApi = new TypeApi()

  export default {
    name: 'AddQuestion',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {

        title: ' سوال',
        selectedExam: {},
        selectedLesson: {},
        types: [],
        filteredTypes: [],
        selectedType: {},
        question: {
          answers: [

            {
              content: '',
              description: '',
            },
            {
              content: '',
              description: '',
            },
            {
              content: '',
              description: '',
            },
            {
              content: '',
              description: '',
            },

          ],

        },
        lessons: [],
        exams: [],
        pageStatus: 'add',
      }
    },
    methods: {
      storeQuestion () {
        var formData = new FormData()
        this.showingProgressBar()
        formData.append('content', this.question.content)
        formData.append('score', this.question.score)
        formData.append('typeId', this.question.typeId.value)
        if(this.question.lesson)
        {
          formData.append('lesson', JSON.stringify(this.question.lesson))

        }
        if(this.question.exam)
        {
          formData.append('exam', JSON.stringify(this.question.exam))
        }
        formData.append('answers', JSON.stringify(this.question.answers))
        formData.append('trueIndex', this.question.trueIndex)
        formData.append('file', this.files)

        questionApi.create(this,formData).then((response)=>{
          this.hiddenProgressBar()
          this.resetPercentage()
          this.question = {}
          this.handleResponse(response.data.status_code, response.data.message)
        });
      },
      updateQuestion (id) {
        this.showLoading()

        var formData = new FormData()
        this.showingProgressBar()
        formData.append('content', this.question.content)
        formData.append('score', this.question.score)
        formData.append('typeId', this.question.typeId.value)
        if(this.question.lesson)
        {
          formData.append('lesson', JSON.stringify(this.question.lesson))

        }
        if(this.question.exam)
        {
          formData.append('exam', JSON.stringify(this.question.exam))
        }
        formData.append('answers', JSON.stringify(this.question.answers))
        formData.append('trueIndex', this.question.trueIndex)
        formData.append('file', this.files)

        questionApi.updateQuestion(this,id,formData).then((response) => {
          this.disableLoading()
          this.question = {}
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      getQuestion (id) {
        questionApi.getQuestion(id).then((response) => {
          this.question = response.data.data
          this.selectedLesson = {
            label: response.data.data.lesson.label,
            value: response.data.data.lesson.value,
          }
          this.selectedExam = {
            label: response.data.data.exam.label,
            value: response.data.data.exam.value,
          }
        })
      },
      searchInArray (labelName, myArray) {
        for (var i = 0; i < myArray.length; i++) {
          if (myArray[i].label === labelName) {
            return myArray[i]
          }
        }
      },
      getTypes () {
        typesApi.getTypes().then((response) => {
          const quizObject = this.searchInArray('quiz', response.data.data)
          const examObject = this.searchInArray('exam', response.data.data)
          this.types.push.apply(this.types, [quizObject, examObject])
        })
      },
      getLessonsSelective () {
        lessonApi.getLessonsSelective().then((response) => {
          this.lessons = response.data.data
        })
      },
      getExamsSelective () {
        examApi.getExamsSelective().then((response) => {
          this.exams = response.data.data
        })
      },
      previewFiles () {
        this.files = this.$refs.myFiles.files[0]
        var sound = document.getElementById('sound')
        var reader = new FileReader()
        reader.onload = function (e) {
          sound.src = this.result
          sound.controls = true
          sound.play()
        }
        reader.readAsDataURL(this.$refs.myFiles.files[0])
      },
    },
    created: function () {
      if (this.$route.params.questionid) {
        this.pageStatus = 'edit'
        this.getQuestion(this.$route.params.questionid)
      }
      this.getLessonsSelective()
      this.getExamsSelective()
      this.getTypes()
    },
    watch: {
      'question.typeId': function () {
        const label = this.question.typeId.label
        if (label == 'exam') {
          this.$refs.lessonSelectDiv.setAttribute('class', 'disableClass')
          this.$refs.quizSelectDiv.classList.remove('disableClass')
        } else {
          this.$refs.quizSelectDiv.setAttribute('class', 'disableClass')
          this.$refs.lessonSelectDiv.classList.remove('disableClass')
        }
      }
    }
  }
</script>
<style scoped>
  label {
    color: #204d74;
  }
  .disableClass {
    pointer-events: none;
    opacity: 0.4;
  }
</style>
