<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div class="row">
      <div class="col-sm-6">
        <form>
          <div class="form-group">
            <label>نام محتواگذار</label>
            <input type="text" name="name" class="form-control" v-model="cp.name"/>
            <span class="input_alert">{{showValidationTitle('name')}}</span>
          </div>

          <div ref="psw">

            <div class="form-group">
              <label>نام کاربری</label>
              <input type="text" name="username" class="form-control" v-model="cp.username"/>
              <span class="input_alert">{{showValidationTitle('username')}}</span>
            </div>

            <div class="form-group">
              <label>کلمه عبور</label>
              <input type="text" name="password" class="form-control" v-model="cp.password"/>
              <span class="input_alert">{{showValidationTitle('password')}}</span>
            </div>
            <div class="form-group">
              <label>تکرار کلمه عبور</label>
              <input type="text" name="passwordRepeat" class="form-control" v-model="cp.passwordRepeat"/>
              <span class="input_alert">{{showValidationTitle('passwordRepeat')}}</span>
            </div>
          </div>

          <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeCp">افزودن</button>
          <button v-else class="btn btn-success" @click.prevent="updateCp(cp._id)">ویرایش</button>
        </form>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import CpApi from '../../services/api/cp/CpApi'
  import myMixin from '../../mixins/myMixin'

  const cpApi = new CpApi()

  export default {
    name: 'AddCp',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' محتوا گذار',
        cp: {},
        pageStatus: 'add',
        loading: false,
      }
    },
    methods: {
      storeCp () {
        if (this.validatePasswords(this.cp.password, this.cp.passwordRepeat)) {
          this.showLoading()
          cpApi.create(this.cp).then((response) => {
            this.disableLoading()
            this.cp = {}
            this.handleResponse(response.data.status_code, response.data.message)
          })
        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
          this.cp = {}
        }
      },
      getCp (id) {
        this.showLoading()
        cpApi.getCp(id).then((response) => {
          this.disableLoading()
          this.cp = response.data.data
        })
      },
      updateCp (id) {
        if (this.validatePasswords(this.cp.password, this.cp.passwordRepeat)) {
          cpApi.updateCp(id, {
            name:this.cp.name
          }).then((response) => {
            this.handleResponse(response.data.status_code, response.data.message)
          })
        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
        }
      },
    },
    created: function () {
      if (this.$route.params.cpid) {
        this.pageStatus = 'edit'
        this.getCp(this.$route.params.cpid)
      }
    },
    mounted () {
      if (this.$route.params.cpid) {
        this.$refs['psw'].classList.add('hidden')
      }
    },
  }
</script>
