<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div class="row">
      <div class="col-sm-6">
        <form>
          <div class="form-group">
            <label>نام </label>
            <input type="text" name="name" class="form-control" v-model="chatAdmin.name"/>
            <span class="input_alert">{{showValidationTitle('name')}}</span>
          </div>
          <div ref="psw">
            <div class="form-group">
              <label>نام کاربری</label>
              <input type="text" name="username" class="form-control" v-model="chatAdmin.username"/>
              <span class="input_alert">{{showValidationTitle('username')}}</span>
            </div>

            <div class="form-group">
              <label>کلمه عبور</label>
              <input type="text" name="password" class="form-control" v-model="chatAdmin.password"/>
              <span class="input_alert">{{showValidationTitle('password')}}</span>
            </div>
            <div class="form-group">
              <label>تکرار کلمه عبور</label>
              <input type="text" name="passwordRepeat" class="form-control" v-model="chatAdmin.passwordRepeat"/>
              <span class="input_alert">{{showValidationTitle('passwordRepeat')}}</span>
            </div>
          </div>
          <div class="form-group">
            <label>جت روم مربوطه</label>
            <v-select multiple  v-model="chatAdmin.chatrooms" :options="chatrooms"></v-select>
          </div>
          <span class="input_alert">{{showValidationTitle('chatroom')}}</span>
          <div class="form-group">
            <label>آپلود عکس </label>
            <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
          </div>
          <span class="input_alert">{{showValidationTitle('file')}}</span>
          <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeChatAdmin">افزودن</button>
          <button v-else class="btn btn-success" @click.prevent="updateChatAdmin(chatAdmin._id)">ویرایش</button>
        </form>
      </div>
    </div>
    <div class="row" style="margin-top: 20px;"
         v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import ChatAdminApi from '../../services/api/chatAdmin/ChatAdminApi'
  import ChatApi from '../../services/api/chat/ChatApi'

  const chatAdminApi = new ChatAdminApi()
  const chatApi = new ChatApi()

  export default {
    name: 'AddChatAdmin',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' ادمین چت',
        chatAdmin: {},
        pageStatus: 'add',
        chatrooms: [],
      }
    },
    methods: {
      storeChatAdmin () {
        if (this.validatePasswords) {
          this.showingProgressBar()
          var formData = new FormData()
          formData.append('name', this.chatAdmin.name)
          formData.append('username', this.chatAdmin.username)
          formData.append('password', this.chatAdmin.password)
          formData.append('file', this.files)
          formData.append('chatrooms', JSON.stringify(this.chatAdmin.chatrooms))
          chatAdminApi.create(this, formData).then((response) => {
            this.hiddenProgressBar()
            this.resetPercentage()
            this.handleResponse(response.data.status_code, response.data.message)
            this.chatAdmin = {}
          })
        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
          this.chatAdmin = {}
        }
      },
      getChatAdmin (id) {
        this.showLoading()
        chatAdminApi.getChatAdmin(id).then((response) => {
          this.disableLoading()
          this.chatAdmin = response.data.data
        })
      },
      updateChatAdmin (id) {
        if (this.validatePasswords) {
          this.showingProgressBar()
          var formData = new FormData()
          formData.append('name', this.chatAdmin.name)
          formData.append('file', this.files)
          formData.append('chatrooms', JSON.stringify(this.chatAdmin.chatrooms))
          chatAdminApi.updateChatAdmin(this, id, formData).then((response) => {
            this.hiddenProgressBar()
            this.resetPercentage()
            this.handleResponse(response.data.status_code, response.data.message)
            this.chatAdmin = {}
          })

        } else {
          this.storeError('passwordRepeat', 'کلمه عبور و تکرار کلمه عبور یکسان نیستند')
          this.chatAdmin = {}
        }
      },
      getChatroomsSelective () {
        chatApi.getChatRoomSelective().then((response) => {
          if (response.data.status_code == 200) {
            this.chatrooms = response.data.data
          }
        })
      }
    },
    mounted () {
      if (this.$route.params.chatAdminId) {
        this.$refs['psw'].classList.add("hidden")
      }
    },
    created () {
      if (this.$route.params.chatAdminId) {
        this.pageStatus = 'edit'
        this.getChatAdmin(this.$route.params.chatAdminId)
      }
      this.getChatroomsSelective()
    }
  }
</script>
