<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row">
        <div class="col-sm-4" v-for="certificate in certificates">
          <div class="inner_certificate_block">
            <div style="text-align: center"><img style="width: 80px;border-radius: 50%" :src="certificate.personalImg">
            </div>
            <table class="table certTable">
              <tr>
                <td>نام :</td>
                <td>{{certificate.firstName}}</td>
              </tr>
              <tr>
                <td>نام خانوادگی :</td>
                <td>{{certificate.lastName}}</td>
              </tr>
              <tr>
                <td>آدرس :</td>
                <td>{{certificate.address}}</td>
              </tr>
              <tr>
                <td>کد پستی :</td>
                <td>{{certificate.zipCode}}</td>
              </tr>
              <tr>
                <td>تاریخ درخواست:</td>
                <td>
                  {{convert_to_jalali(certificate.time)}}
                </td>
              </tr>
              <tr>
                <td>وضعیت :</td>
                <td v-if="certificate.status == 'reject' "
                    style="color:#ac2925;border: 1px solid #ac2925;text-align: center">{{certificate.status}}
                </td>
                <td v-else-if="certificate.status == 'sent' "
                    style="color:lawngreen;border: 1px solid lawngreen;text-align: center">{{certificate.status}}
                </td>
                <td v-else="certificate.status == 'pending' "
                    style="color: #ec971f;border: 1px solid #ec971f;text-align: center">{{certificate.status}}
                </td>
              </tr>
            </table>
            <table class="table">
              <tr>
                <td>
                  <router-link :to="{name:'ShowSingleCertificate',params:{certId:certificate._id}}"><i
                    class="fa fa-edit"> مشاهده اطلاعات تکمیلی </i>
                  </router-link>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import CertificateApi from '../../services/api/certificate/CertificateApi'
  import myMixin from '../../mixins/myMixin'
  import jalian from 'moment-jalaali'

  const certificateApi = new CertificateApi()

  export default {
    name: 'ListCertificates',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست مدرک ها',
        certificates: {},
        pageStatus: 'list',
        loading: false,
        page: 1,
        limit: 3,
        totalPage: 1,
      }
    },
    methods: {
      clickCallback (pageNum) {
        this.page = pageNum
        this.certificates = {}
        this.getCertificates()
      },
      getCertificates () {
        this.showLoading()
        certificateApi.getCertificatesByPaginate(this.page, this.limit).then((response) => {
          this.disableLoading()
          if (response.data.status_code == 200) {
            this.certificates = response.data.data
            this.totalPage = response.data.totalPage
          }
        })
      },
      convert_to_jalali ($timestamp) {
        return jalian.utc($timestamp).format('jYYYY/jM/jD')
      }
    },
    created () {
      this.getCertificates()
    },
  }
</script>
