<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered">
            <tr>
              <th>نام چت روم</th>
              <th>ساعت شروع</th>
              <th>ساعت پایان</th>
              <th>سطح مربوطه</th>
              <th>درس مربوطه</th>
              <th>کاور عکس</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="chatroom in chatrooms">
              <td>{{chatroom.title}}</td>
              <td>{{chatroom.startTime}}</td>
              <td>{{chatroom.endTime}}</td>
              <td>{{getValueIfNotUndifiend(chatroom.level.label)}}</td>
              <td>{{getValueIfNotUndifiend(chatroom.lesson.label)}}</td>
              <td>
                <img class="thumb" v-bind:src="chatroom.avatarUrl">
              </td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditChatroom',params:{chatroomId:chatroom._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteChatroom(chatroom._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6">
          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import ChatroomApi from '../../services/api/chatroom/ChatroomApi'

  const chatroomApi = new ChatroomApi()

  export default {
    name: 'ListLessons',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست چت روم ها',
        chatrooms: {},
        pageStatus: 'listChatrooms',
        loading: false,
        page: 1,
        limit: 5,
        totalPage: 1
      }
    },
    methods: {

      clickCallback (pageNum) {
        this.page = pageNum
        this.chatrooms = {}
        this.getChatrooms()
      },
      getChatrooms () {
        this.showLoading()
        chatroomApi.getChatroomsByPaginate(this.page, this.limit).then((response) => {
          this.disableLoading()
          this.chatrooms = response.data.data
          this.totalPage = response.data.totalPage
        })
      },
      deleteChatroom (id) {
        chatroomApi.deleteChatroom(id).then((response) => {
          this.handleResponse(response.data.status_code, response.data.message)
          this.getChatrooms()
        })
      },
      getValueIfNotUndifiend (data) {
        if (data) {
          return data
        } else {
          return ''
        }
      },
    },
    created () {
      this.getChatrooms()
    }
  }
</script>
