<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form>
      <div class="row">
        <div class="col-sm-6">
          <h6>اطلاعات اصلی :</h6>
          <div class="inner_certificate_block_single">
            <div style="text-align: center"><img style="width: 150px;border-radius: 50%" :src="certificate.personalImg">
            </div>
            <table class="table table-success certTable">
              <tr>
                <td>نام :</td>
                <td>{{certificate.firstName}}</td>
              </tr>
              <tr>
                <td>نام خانوادگی :</td>
                <td>{{certificate.lastName}}</td>
              </tr>
              <tr>
                <td>آدرس :</td>
                <td>{{certificate.address}}</td>
              </tr>
              <tr>
                <td>کد پستی :</td>
                <td>{{certificate.zipCode}}</td>
              </tr>
              <tr>
                <td>تاریخ درخواست:</td>
                <td></td>
              </tr>
              <tr>
                <td>وضعیت :</td>
                <td>
                    <div class="form-group">
                      <select v-model="certificate.status" class="form-control">
                        <option value="pending" style="color: #ec971f">در حال بررسی</option>
                        <option value="reject" style="color: indianred">تایید نشده </option>
                        <option value="sent" style="color: lawngreen">ارسال شده</option>
                      </select>
                    </div>
                </td>
              </tr>
            </table>
          </div>
        </div>
        <div class="col-sm-6">
          <h6>تصویر کارت ملی :</h6>
          <div style="text-align: center"><img width="100%" height="340px;" :src="certificate.IDCard" class="img-rounded">
          </div>
        </div>
      </div>
      <button style="margin-top: 30px;" class="btn btn-success" @click.prevent="updateCertificate(certificate._id)">
        ویرایش
      </button>
    </form>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]" style="margin-top: 20px;">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import CertificateApi from '../../services/api/certificate/CertificateApi'
  const certificateApi = new CertificateApi()

  export default {
    name: 'ShowSingleCertificate',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' مدرک',
        loading: false,
        certificate: {},
        pageStatus: 'edit',
      }
    },
    methods: {
      getCertificate (id) {
        certificateApi.getCertificate(id).then((response) => {
          this.certificate = response.data.data
        })
      },
      updateCertificate (id) {
        this.showLoading()
        certificateApi.updateCertificate(id,this.certificate).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
        });
      },
    },
    created () {
      if (this.$route.params.certId) {
        this.getCertificate(this.$route.params.certId)
      }
    }
  }
</script>
