<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]" style="margin-top: 20px;">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
    <div class="message_containers">
      <div class="row" v-for="message in ticket.msg">
        <div class="col-sm-8" :class="getClassOfElement(message.sender)">
          <div class="inner_msg_block_student">
            <div class="card-header msgHeaderDiv">
              <table class="table msgHeaderTable">
                <tr>
                  <td>ارسال کننده :</td>
                  <td v-if="message.sender == 'student'"> {{getStudentFullName()}}</td>
                  <td v-else="message.sender == 'supporter'"> {{getCurrentSupporterName()}}</td>
                  <td>تاریخ پیام :</td>
                  <td>{{convert_to_jalali(message.time)}}</td>
                </tr>
              </table>
            </div>
            <div class="card-body msgBody">
              <img v-if="message.sender == 'student'" style="width:50px;border-radius: 50%" :src="senderAvatar">
              <img v-else="message.sender == 'supporter'" style="width:50px;border-radius: 50%" :src="supporterAvar">
              <hr>
              {{message.description}}
            </div>
            <div class="card-footer footerMsg">
              <a v-if="message.image" :href="message.image"><i class="fa fa-edit"> دانلود تصویر </i></a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="editor_container">
      <label style="margin-top: 5px;">لطفا متن پیام خود را در کادر زیر وارد کنید</label>
      <div class="form-group">
        <textarea v-model="textareaContent" class="form-control textAreaSend" rows="5"></textarea>
      </div>
      <a class="btn btn-success" @click.prevent="sendMessage()"><i class="fa fa-send"> ارسال </i></a>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import TicketApi from '../../services/api/ticket/TicketApi'
  const ticketApi = new TicketApi()
  import jalian from 'moment-jalaali'
  export default {
    name: 'ListMessages',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' جزیات تیکت',
        loading: false,
        ticket: {},
        senderName: '',
        senderLastName: '',
        senderAvatar: '',
        supporterAvar: '',
        textareaContent: '',
        user: JSON.parse(localStorage.getItem('user')),
        pageStatus: 'edit',
      }
    },
    methods: {
      convert_to_jalali ($timestamp) {
        return jalian.utc($timestamp).format('jYYYY/jM/jD HH:MM:SS')
      },
      getTicket (id) {
        this.showLoading()
        ticketApi.getTicket(id).then((response) => {
          this.disableLoading()
          this.ticket = response.data.data
          this.senderName = response.data.data.student.fname
          this.senderLastName = response.data.data.student.lname
          this.senderAvatar = response.data.data.student.avatarUrl
          this.supporterAvar = response.data.data.supporter.avatarUrl
        })
      },
      sendMessage () {
        this.showLoading()
        ticketApi.updateTicket(this.ticket._id, {
          'newMsg': true,
          'status': 'answered',
          'msg': {
            'sender': 'supporter',
            'description': this.textareaContent
          }
        }).then((response) => {
          this.disableLoading()
          this.getTicket(this.ticket._id)
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
      viewMessage () {
        ticketApi.updateTicket(this.$route.params.ticketId, {
          'viewed': true
        }).then((response) => {
        })
      },
      getClassOfElement ($sender) {
        var cls = ''
        switch ($sender) {
          case 'student':
            cls = 'col-sm-offset-4'
            break
          case 'supporter':
            cls = ''
            break
        }
        return cls
      },
      getStudentFullName () {
        return this.senderName + ' ' + this.senderLastName
      },
      getCurrentSupporterName () {
        return this.user.name
      }
    },
    created () {
      if (this.$route.params.ticketId) {
        this.getTicket(this.$route.params.ticketId)
        this.viewMessage();
      }
    }
  }
</script>
