<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>عنوان</th>
            <!--<th>عملیات</th>-->
          </tr>
          <tbody>
          <tr v-for="category in categories">
            <td>{{category.label}}</td>
            <!--<td>-->
              <!--<div class="op-icon">-->
                <!--<a @click.prevent="deleteCategory(category.value)"><i class="fa fa-remove deleteIcon"></i></a>-->
              <!--</div>-->
            <!--</td>-->
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import CategoryApi from '../../services/api/category/CategoryApi'
  import myMixin from '../../mixins/myMixin'
  const categoryApi = new CategoryApi()

  export default {
    name: 'ListCategory',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست نوع دسته بندی',
        categories: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getCategories () {
        this.showLoading();
        categoryApi.getCategories().then((response) => {
          this.categories = response.data.data
          this.disableLoading();
        })
      },
      deleteCategory (id) {
        this.showLoading();
        categoryApi.deleteCategory(id).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getPackages();
        })
      },
    },
    created () {
      this.getCategories()
    }
  }
</script>
