<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>نام سطح</label>
        <input type="text" name="title" class="form-control" v-model="level.title"/>
        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>
      <div class="form-group">
        <label>توضیحات سطح</label>
        <input type="text" name="description" class="form-control" v-model="level.description"/>
        <span class="input_alert">{{showValidationTitle('description')}}</span>
      </div>
      <div class="form-group">
        <label>ترتیب سطح</label>
        <select class="form-control" v-model="level.order">
          <option value="1">اول</option>
          <option value="2">دوم</option>
          <option value="3">سوم</option>
          <option value="4">چهارم</option>
          <option value="5">پنجم</option>
          <option value="6">ششم</option>
          <option value="7">هفتم</option>
          <option value="8">هشتم</option>
          <option value="9">نهم</option>
          <option value="10">دهم</option>
        </select>
        <span class="input_alert">{{showValidationTitle('order')}}</span>
      </div>
      <div class="form-group">
        <label>آپلود عکس </label>
        <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
      </div>
      <span class="input_alert">{{showValidationTitle('file')}}</span>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeLevel">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateLevel(level._id)">ویرایش</button>
    </form>
    <div class="row" style="margin-top: 20px;"
         v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import LevelApi from '../../services/api/level/LevelApi'

  const levelApi = new LevelApi()

  export default {
    name: 'AddLevel',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' سطح',
        level: {},
        pageStatus: 'add',
      }
    },
    methods: {
      storeLevel () {

        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.level.title)
        formData.append('description', this.level.description)
        formData.append('order', this.level.order)
        formData.append('file', this.files)

        levelApi.create(this, formData).then((response) => {
          this.hiddenProgressBar()
          this.resetPercentage()
          this.level = {}
          this.handleResponse(response.data.status_code, response.data.message)
        })

      },
      getLevel (id) {
        levelApi.getLevel(id).then((response) => {
          this.level = response.data.data
        })
      },
      updateLevel (id) {
        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.level.title)
        formData.append('description', this.level.description)
        formData.append('order', this.level.order)
        formData.append('file', this.files)

        levelApi.updateLevel(this,id,formData).then((response)=>{
          this.hiddenProgressBar()
          this.resetPercentage()
          this.level = {}
          this.handleResponse(response.data.status_code, response.data.message)
        })
      },
    },
    created () {
      if (this.$route.params.lvlid) {
        this.pageStatus = 'edit'
        this.getLevel(this.$route.params.lvlid)
      }
    }
  }
</script>
