import axios from 'axios'

const API_URL = ''
const CHATROOM_SELECTIVE = 'https://academi.rahasahamrah.com/api/chatroom/selective/'

export default class ChatApi {

  getChatRoomSelective () {

    return axios.get(CHATROOM_SELECTIVE,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );

  }


}
