<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>نام چت روم</label>
        <input type="text" name="title" class="form-control" v-model="chatroom.title"/>
        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>
      <div class="form-group">
        <label>ساعت شروع</label>
        <input type="text" name="description" class="form-control" v-model="chatroom.startTime"/>
        <span class="input_alert">{{showValidationTitle('startTime')}}</span>
      </div>
      <div class="form-group">
        <label>ساعت پایان</label>
        <input type="text" name="description" class="form-control" v-model="chatroom.endTime"/>
        <span class="input_alert">{{showValidationTitle('endTime')}}</span>
      </div>
      <div class="form-group">
        <label>چت روم برای کدام است ؟</label>
        <input checked style="margin-right: 20px;" type="radio" value="level" v-model="chatroom.kind"/> <span
        style="margin-right:10px;">سطح</span>
        <input style="margin-right: 20px;" type="radio" value="lesson" v-model="chatroom.kind"/> <span
        style="margin-right: 10px;">درس</span>
      </div>
      <div class="form-group" ref="levelSelectiveDiv">
        <label> سطح مربوطه</label>
        <v-select v-if="pageStatus === 'add' " v-model="chatroom.level" :options="levels"></v-select>
        <v-select v-else :value="selectedLevel" v-model="selectedLevel" :options="levels"></v-select>
        <span class="input_alert">{{showValidationTitle('level')}}</span>
      </div>
      <div class="form-group" ref="lessonSelectDiv">
        <label> درس مربوطه</label>
        <v-select v-if="pageStatus === 'add' " v-model="chatroom.lesson" :options="lessons"></v-select>
        <v-select v-else :value="selectedLesson" v-model="selectedLesson" :options="lessons"></v-select>
        <span class="input_alert">{{showValidationTitle('lesson')}}</span>
      </div>
      <div class="form-group">
        <label>آپلود عکس </label>
        <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
      </div>
      <span class="input_alert">{{showValidationTitle('file')}}</span>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeChatroom">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateChatroom(chatroom._id)">ویرایش</button>
    </form>
    <div class="row" style="margin-top: 20px;"
         v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import LessonApi from '../../services/api/lesson/LessonApi'
  import LevelApi from '../../services/api/level/LevelApi'
  import ChatroomApi from '../../services/api/chatroom/ChatroomApi'

  const lessonApi = new LessonApi()
  const levelApi = new LevelApi()
  const chatroomApi = new ChatroomApi()

  export default {
    name: 'AddChatroom',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' درس',
        chatroom: {
          kind:""
        },
        levels: [],
        lessons: [],
        selectedLesson: {},
        selectedLevel: {},
        pageStatus: 'add',
      }
    },
    methods: {
      storeChatroom () {
        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.chatroom.title)
        formData.append('startTime', this.chatroom.startTime)
        formData.append('endTime', this.chatroom.endTime)
        if (this.chatroom.kind == 'level') {
          formData.append('level', JSON.stringify(this.chatroom.level))
        }
        if (this.chatroom.kind == 'lesson') {
          formData.append('lesson', JSON.stringify(this.chatroom.lesson))
        }
        formData.append('file', this.files)
        chatroomApi.create(this,formData).then((response)=>{
            this.hiddenProgressBar()
            this.resetPercentage()
            this.handleResponse(response.data.status_code, response.data.message)
            this.chatroom = {}
        });
      },
      getChatroom (id) {
        chatroomApi.getChatroom(id).then((response) => {
          this.chatroom = response.data.data
          this.selectedLesson = {
            label: response.data.data.lesson.label,
            value: response.data.data.lesson.value,
          }
          this.selectedLevel = {
            label: response.data.data.level.label,
            value: response.data.data.level.value,
          }
        })

      },
      updateChatroom (id) {

        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.chatroom.title)
        formData.append('startTime', this.chatroom.startTime)
        formData.append('endTime', this.chatroom.endTime)
        if (this.chatroom.kind == 'level') {
          formData.append('level', JSON.stringify(this.selectedLevel))
        }
        if (this.chatroom.kind == 'lesson') {
          formData.append('lesson', JSON.stringify(this.selectedLesson))
        }
        formData.append('file', this.files)
        chatroomApi.updateChatroom(this, id , formData).then((response)=>{
          this.hiddenProgressBar()
          this.resetPercentage()
          this.handleResponse(response.data.status_code, response.data.message)
          this.chatroom = {}
        });
      },
      isEmptyObject (obj) {
        for (var prop in obj) {
          if (obj.hasOwnProperty(prop)) {
            return false
          }
        }
        return JSON.stringify(obj) === JSON.stringify({})
      },
      getLevelsSelective () { //for select box
        levelApi.getLevelsSelective().then((response) => {
          this.levels = response.data.data
        })
      },
      getLessonsSelective () {
        lessonApi.getLessonsSelective().then((response) => {
          this.lessons = response.data.data
        })
      },

    },

    watch: {
      'chatroom.kind': function () {
        const kind = this.chatroom.kind
        if (kind == 'level') {
          this.$refs.lessonSelectDiv.setAttribute('class', 'disableClass')
          this.$refs.levelSelectiveDiv.classList.remove('disableClass')

        } else {
          this.$refs.levelSelectiveDiv.setAttribute('class', 'disableClass')
          this.$refs.lessonSelectDiv.classList.remove('disableClass')
        }
      }
    },
    created () {
      if (this.$route.params.chatroomId) {
        this.pageStatus = 'edit'
        this.getChatroom(this.$route.params.chatroomId)
      }
      this.getLessonsSelective()//for select box
      this.getLevelsSelective()//for select box
    }
  }
</script>
<style scoped>
  .disableClass {
    pointer-events: none;
    opacity: 0.4;
  }
</style>
