<template>
  <!-- Modal -->
  <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
       aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">تغییر وضعیت پیام</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <button @click="pinMsg" type="button" class="btn btn-primary">پین کردن</button>
          <button @click="deleteMsg" type="button" class="btn btn-danger" style="margin-right:15px;">حذف کردن</button>
          <button @click="openWarnMsgConatiner" type="button" class="btn btn-warning" style="margin-right:15px;">اخطار دادن</button>
          <button @click="openBlockDurationBox" type="button" class="btn btn-danger" style="margin-right:15px;">بلاک
            کردن
          </button>
          <button @click="unBlock" type="button" class="btn btn-success" style="margin-right:15px;">آزاد کردن کاربر
          </button>
          <br>

          <div class="warnContainer" v-bind:class="{ showBox: isActive }">
            <h5>لطفا متن اخطار را در کادر زیر وارد کنید</h5>
            <input type="text" class="form-control" v-model="warnMsg">
            <br>
            <button @click="warnStudent" type="button" class="btn btn-warning" style="margin-right:15px;">ثبت اخطار</button>
          </div>


          <br>
          <div class="blockTimeContainer" v-bind:class="{ showBox: isActive }">
            <h5>لطفا مدت زمان بلاک شدن کاربر را انتخاب کنید</h5>
            <input checked style="margin-right: 20px;" type="radio" value="24" v-model="blockDuration"><label>24
            ساعت</label>
            <input style="margin-right: 20px;" type="radio" value="48" v-model="blockDuration"><label>48 ساعت</label>
            <input style="margin-right: 20px;" type="radio" value="72" v-model="blockDuration"><label>72 ساعت</label>
            <input style="margin-right: 20px;" type="radio" value="-1" v-model="blockDuration"><label>بلاک برای
            همیشه</label>
            <br>
            <button @click="blockStudent" type="button" class="btn btn-danger" style="margin-right:15px;">ثبت بلاک</button>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal">بستن</button>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
  export default {
    name: 'modal',
    props: ['msg'],
    data () {
      return {
        blockDuration: 24,
        isActive: false,
        warnMsg:''
      }
    },
    methods: {
      deleteMsg: function () {
        this.$emit('deleteMsg', this.msg)
      },
      pinMsg: function () {
        this.$emit('pinMsg', this.msg)
      },
      warnStudent: function () {
        this.msg.warnMsg = this.warnMsg
        this.$emit('warnStudent', this.msg)
        this.isActive = false
        this.warnMsg = ''
      },
      openBlockDurationBox: function () {
        this.isActive = !this.isActive
      },
      openWarnMsgConatiner:function(){
        this.isActive = !this.isActive
      },
      blockStudent: function () {
        this.msg.blocked = this.blockDuration
        this.$emit('blockStudent', this.msg)
        this.isActive = false
     },
      unBlock: function () {
        this.blockDuration = 0
        this.blockStudent()
        this.isActive = false
      }

    }

  }
</script>

<style scoped>
  .showBox {
    display: block !important;
  }
</style>
