import axios from 'axios'

const API_URL = 'https://academi.rahasahamrah.com/api/package/'
export default class PackageApi {

  create (packageItem) {
    if (packageItem.off.percent === " ") {
      delete packageItem.off
    }

    return axios.post(API_URL,
      packageItem,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      })
  }

  getPacakge (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })
  }

  updatePackage (id, packageItem) {
    return axios.put(API_URL + id, packageItem, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })

  }

  getPackages () {

    return axios.get(API_URL,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }

  deletePackage (id) {
    return axios.delete(API_URL + id,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }

}
