<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام محتواگذار</th>
            <th>نام کاربری</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="cp in cps">
            <td>{{cp.name}}</td>
            <td>{{cp.username}}</td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditCp',params:{cpid:cp._id}}"><i class="fa fa-edit"></i></router-link>
                <a @click.prevent="deleteCp(cp._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>
  </div>
</template>
<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import CpApi from '../../services/api/cp/CpApi'
  import myMixin from '../../mixins/myMixin'
  const cpApi = new CpApi()
  export default {
    name: 'ListCp',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست محتواگذار ها',
        cps: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {
      getCps () {
        this.showLoading();
        cpApi.getCps().then((response) => {
          this.cps = response.data.data
          this.disableLoading();
        })
      },
      deleteCp (id) {
        this.showLoading();
        cpApi.deleteCp(id).then((response) => {
          this.disableLoading()
          this.handleResponse(response.data.status_code, response.data.message)
          this.getCps()
        })
      },
    },
    created () {
      this.getCps()
    }
  }
</script>
