import axios from 'axios'

const API_URL = "https://academi.rahasahamrah.com/api/ticket/department/";
const TYPES_SELECTIVE = "https://academi.rahasahamrah.com/api/ticket/department/";

export default class DepartmentApi {

  create (department) {
    return axios.post(API_URL,
      department,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      })
  }

  getDepartment (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    });
  }



  updateDepartment (id,department) {
    return axios.put(API_URL + id, department, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    });

  }

  getDepartments(){

    return axios.get(API_URL,
      {
        headers: {"Authorization":'Bearer ' + localStorage.getItem('jwt')}
      }
    );
  }


  deleteDepartment(id)
  {
    return axios.delete(API_URL + id,
      {
        headers: {"Authorization":'Bearer ' + localStorage.getItem('jwt')}
      }
    );
  }


  getDepartmentsSelective () {

    return axios.get(TYPES_SELECTIVE,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );

  }


}
