<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>آپلود عکس </label>
        <input type="file" id="file" ref="myFiles" class="custom-file-input" @change="previewFiles">
      </div>
      <span class="input_alert">{{showValidationTitle('file')}}</span>
      <div class="form-group">
        <label>نام آزمون</label>
        <input type="text" name="title" class="form-control" v-model="exam.title"/>
        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>
      <div class="form-group">
        <label>زمان أزمون</label>
        <input type="text" name="time" class="form-control" v-model="exam.time"/>
        <span class="input_alert">{{showValidationTitle('time')}}</span>
      </div>
      <div class="form-group">
        <label>انتخاب درس مربوطه</label>
        <v-select v-if="pageStatus === 'add' " v-model="exam.lesson" :options="lessons"></v-select>
        <v-select v-else :value="selectedLesson" v-model="selectedLesson" :options="lessons"></v-select>
      </div>
      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeExam">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateExam(exam._id)">ویرایش</button>
    </form>
    <div class="row" style="margin-top: 20px;"
         v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">
        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import myMixin from '../../mixins/myMixin'
  import LessonApi from '../../services/api/lesson/LessonApi'
  import ExamApi from '../../services/api/exam/ExamApi'

  const lessonApi = new LessonApi()
  const examApi = new ExamApi()

  export default {
    name: 'AddExam',
    mixins: [myMixin],
    components: { Breadcrumb },
    data () {
      return {
        title: ' آزمون ',
        exam: {},
        lessons: [],
        selectedLesson: {},
        pageStatus: 'add',
      }
    },
    methods: {
      storeExam () {
        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.exam.title)
        formData.append('time', this.exam.time)
        formData.append('preLesson', JSON.stringify(this.exam.lesson))
        formData.append('file', this.files)
        examApi.create(this, formData).then((response) => {
          this.hiddenProgressBar()
          this.resetPercentage()
          this.handleResponse(response.data.status_code, response.data.message)
          this.exam = {}
        })
      },
      getExam (id) {
        examApi.getExam(id).then((response) => {
          this.exam = response.data.data
          this.selectedLesson = {
            label: response.data.data.preLesson.label,
            value: response.data.data.preLesson.value,
          }
        })
      },
      updateExam (id) {
        this.showingProgressBar()
        var formData = new FormData()
        formData.append('title', this.exam.title)
        formData.append('time', this.exam.time)
        formData.append('preLesson', JSON.stringify(this.selectedLesson))
        formData.append('file', this.files)
        examApi.updateExam(this, id ,  formData).then((response) => {
          this.hiddenProgressBar()
          this.resetPercentage()
          this.handleResponse(response.data.status_code, response.data.message)
          this.exam = {}
        })
      },
      getLessonsSelective () {
        lessonApi.getLessonsSelective().then((response) => {
          this.lessons = response.data.data
        })
      },
    },
    created () {
      if (this.$route.params.examid) {
        this.pageStatus = 'edit'
        this.getExam(this.$route.params.examid)
      }
      this.getLessonsSelective()
    }
  }
</script>
